import * as Sentry from "@sentry/browser";
let trackExtraDetails = {};
export const init = (initTrackExtraDetails = {}) => {
    trackExtraDetails = initTrackExtraDetails;
};
export const track = (event, properties = {}) => {
    properties = Object.assign({}, properties, trackExtraDetails);
    if (IS_PRODUCTION_ENV !== "true") {
        console.log("Event tracked: %c%s", "font-weight: 600", event, properties);
        return;
    }
    const apiGwIframe = document.getElementById("api-gw");
    if (apiGwIframe) {
        apiGwIframe.contentWindow.postMessage({ event, properties, type: "analytics" }, "*");
    }
    else {
        window.analytics && window.analytics.track(event, properties);
    }
};
export const identify = (userId) => {
    if (IS_PRODUCTION_ENV !== "true") {
        console.log("User identified with ID: %c%s", "font-weight: 600", userId);
        return;
    }
    const apiGwIframe = document.getElementById("api-gw");
    if (apiGwIframe) {
        apiGwIframe.contentWindow.postMessage({ userId, type: "analytics-identify" }, "*");
    }
    else {
        window.analytics && window.analytics.identify(userId);
    }
    userId && Sentry.setUser({ id: userId });
};
