import { scenegraph } from '../sdk';
import { getNodeType, translateNodeTypeToFigmaType, getNodePath, isNodeVisible, treeDeepSearch, getNodeByGUID, isNodeExportable } from './utils/xd';
const mapNodeToFigmaComponent = (node) => {
    const nodePath = getNodePath(node);
    return Object.assign({
        figmaTextId: node.guid,
        nodeId: node.guid,
        type: translateNodeTypeToFigmaType(getNodeType(node)),
        path: nodePath
    }, getNodeType(node) === 'Text' ? { text: node.text } : {});
};
export function handleSelectionChange(filterType) {
    const rawSelection = scenegraph.selection.items;
    let selectedComponents = [];
    if (filterType === "TOP_LEVEL_FRAMES_OR_INNER_TEXTS") {
        selectedComponents = [].concat(rawSelection.filter(node => isNodeExportable(node)), treeDeepSearch(rawSelection.filter(node => !isNodeExportable(node)), node => getNodeType(node) === 'Text' && isNodeVisible(node)))
            .filter((node) => {
            const nodePath = getNodePath(node);
            const textParentFrameNode = nodePath[1] && getNodeByGUID(nodePath[1].id);
            return isNodeExportable(node) || (textParentFrameNode && isNodeExportable(textParentFrameNode));
        })
            .map(mapNodeToFigmaComponent);
    }
    return selectedComponents;
}
