import * as React from "react";
import "./ChangeSubmitForm.scss";
import Button from "../Button/Button";
export default class ChangeSubmitForm extends React.Component {
    constructor(props) {
        super(props);
        this.inputNote = null;
        this.handleFormSubmit = this.handleFormSubmit.bind(this);
    }
    handleFormSubmit(e) {
        const { onSubmit } = this.props;
        const noteInputEl = e.target.elements.note;
        const note = noteInputEl && noteInputEl.value.replace(/\s+/g, ' ').trim();
        e.preventDefault();
        onSubmit({ note });
    }
    render() {
        const { isError, isLoading, isTextChanged } = this.props;
        return React.createElement("form", { className: "change-submit-form", onSubmit: this.handleFormSubmit },
            isTextChanged && React.createElement("input", { ref: el => this.inputNote = el, className: "input", type: "text", name: "note", minLength: 4, maxLength: 300, disabled: !isTextChanged, placeholder: "Add a note about the change" }),
            React.createElement(Button, { theme: "primary", className: "submit-button", type: "submit", isLoading: isLoading, isDisabled: !isTextChanged }, "Submit Change"),
            isError && React.createElement("div", { className: "error" },
                "Oops... Something went wrong. Contact us at ",
                React.createElement("a", { href: "mailto:support@frontitude.com", target: "_blank" }, "support@frontitude.com"),
                "."));
    }
}
