import { PUBLISH_REACTIVE_ACTION } from "../constants/PluginMessageTypes";
import { isReactiveActionType } from "../utils/misc";
import * as logger from "../../../commons/utils/logger";
import { platformAPI } from '../platformAPI';
export default store => next => async (action) => {
    if (!platformAPI.getPlatformSettings().isSupportsReactiveActions) {
        return next(action);
    }
    const rootState = store.getState();
    const { app } = rootState;
    const { config, account } = app;
    const { reactiveActionsPublisherId } = config;
    const { type, payload } = action;
    const isReactiveAction = isReactiveActionType(type);
    const isReplayedAction = payload && payload.isReplayed;
    if (isReactiveAction && !isReplayedAction && reactiveActionsPublisherId) {
        const actionWithPublisherClientId = Object.assign({}, action, { publisherId: reactiveActionsPublisherId });
        parent.postMessage({ pluginMessage: { type: PUBLISH_REACTIVE_ACTION, action: actionWithPublisherClientId } }, "*");
    }
    else if (isReactiveAction && !isReplayedAction && !reactiveActionsPublisherId && account && account.collaborators.length > 1) {
        logger.error(new Error("Could not publish reactive action since no publisher ID found"), {
            action,
            config
        });
    }
    return next(action);
};
