import * as React from "react";
import "./IntroScreen.scss";
import "./IntroScreen.xd.scss";
import AvatarPlaceholder from "../../../../commons/assets/avatar.svg";
import GoogleLoginButton from "../GoogleLoginButton/GoogleLoginButton";
import Button from "../Button/Button";
import { bindActionCreators } from "redux";
import * as AppActions from "../../store/app/actions";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { generateRandomString } from "../../utils/misc";
import { TERMS_OF_SERVICE, PRIVACY_POLICY, WEBSITE_HOMEPAGE } from "../../constants/EndpointTypes";
import FullScreenLoader from "../FullScreenLoader/FullScreenLoader";
import { platformAPI } from '../../platformAPI';
import { GOOGLE_AUTH, MICROSOFT_AZURE_AUTH } from "../../constants/AuthenticationProviderTypes";
import * as logger from "../../../../commons/utils/logger";
const sessionId = generateRandomString();
class IntroScreen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false
        };
        this.handleGoogleSignInClick = this.handleGoogleSignInClick.bind(this);
        this.handleGoogleSignInCancelClick = this.handleGoogleSignInCancelClick.bind(this);
    }
    handleGoogleSignInClick(authProviderType) {
        this.setState({ isLoading: true }, async () => {
            const { appActions } = this.props;
            const { login, promptBell, hideBell } = appActions;
            try {
                await login(sessionId, authProviderType);
            }
            catch (error) {
                logger.error(error);
                this.setState({ isLoading: false }, () => {
                    promptBell({
                        type: "error",
                        message: "Something went wrong. Please contact support@frontitude.com",
                        duration: 30 * 1000,
                        onClose: hideBell
                    });
                });
            }
        });
    }
    handleGoogleSignInCancelClick() {
        this.setState({ isLoading: false });
    }
    render() {
        const { isLoading } = this.state;
        return React.createElement("div", { className: "intro-screen plugin-tab" },
            isLoading &&
                React.createElement(FullScreenLoader, { title: "Signing in...", desc: "A browser window should now be open prompting you to sign in", Button: ({ className }) => {
                        return React.createElement(Button, { theme: "secondary", className: `${className} cancel-button`, onClick: this.handleGoogleSignInCancelClick }, "Cancel");
                    } }),
            !isLoading &&
                React.createElement(React.Fragment, null,
                    React.createElement(AvatarPlaceholder, null),
                    React.createElement("div", { className: "tab-title" },
                        "Sign in to supercharge your text management over ",
                        platformAPI.getPlatformTerminology().displayName),
                    React.createElement("div", { className: "tab-desc" }, "Manage all your UX copy in one place, in a text-first environment"),
                    React.createElement(GoogleLoginButton, { onClick: () => this.handleGoogleSignInClick(GOOGLE_AUTH) }),
                    React.createElement("div", { className: 'ms-azure-login-button', onClick: () => this.handleGoogleSignInClick(MICROSOFT_AZURE_AUTH) }, "Sign in with Microsoft"),
                    React.createElement("div", { className: "terms-desc" },
                        "By signing in you accept Frontitude's ",
                        React.createElement("a", { className: "link", href: PRIVACY_POLICY, target: "_blank" }, "Privacy policy"),
                        " and ",
                        React.createElement("a", { className: "link", href: TERMS_OF_SERVICE, target: "_blank" }, "Terms of service")),
                    React.createElement("div", { className: "learn-more-link" },
                        React.createElement("a", { href: WEBSITE_HOMEPAGE + "?utm_source=plugin-intro-screen", target: "_blank" }, "Learn more about Frontitude"))));
    }
}
const mapStateToProps = ({}) => {
    return {};
};
const mapDispatchToProps = (dispatch) => {
    return {
        appActions: bindActionCreators(AppActions, dispatch)
    };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(IntroScreen));
