import { Constants } from './constants';
export const initialPullConfig = {
    textStatus: null
};
const initialState = {
    user: null,
    bell: {
        type: "",
        message: "",
        onClose: null
    },
    config: {
        env: null,
        reactiveActionsPublisherId: "",
        pullChanges: initialPullConfig
    },
    lastStateFromServerSetAt: 0,
    featureRequests: [],
    account: null,
    isOnboardingMode: false
};
export function appReducer(state = initialState, action) {
    switch (action.type) {
        case Constants.SET_USER_DETAILS:
            return Object.assign({}, state, {
                user: action.payload
            });
        case Constants.REQUEST_FEATURE:
            return Object.assign({}, state, {
                featureRequests: state.featureRequests.concat(action.payload)
            });
        case Constants.SET_STATE:
            const newState = action.payload;
            const appState = newState.app;
            const { exportedFrames } = newState.text;
            window.__FRONTITUDE_ENV = appState.config.env;
            return Object.assign({}, state, appState, {
                isOnboardingMode: Object.keys(exportedFrames).length === 0
            });
        case Constants.SET_ONBOARDING_MODE:
            return Object.assign({}, state, {
                isOnboardingMode: action.payload
            });
        case Constants.SHOW_BELL:
            const { type, message, onClose } = action.payload;
            return Object.assign({}, state, {
                bell: {
                    type,
                    message,
                    onClose
                }
            });
        case Constants.HIDE_BELL:
            return Object.assign({}, state, {
                bell: {
                    type: "",
                    message: "",
                    onClose: null
                }
            });
        case Constants.SET_REACTIVE_ACTIONS_PUBLISHER_ID:
            const { publisherId } = action.payload;
            return Object.assign({}, state, {
                config: Object.assign({}, state.config, {
                    reactiveActionsPublisherId: publisherId
                })
            });
        case Constants.COMPLETE_TOUR:
            const { tourId } = action.payload;
            const currentCompletedTours = state.user.completedTourIds || [];
            return Object.assign({}, state, {
                user: Object.assign({}, state.user, {
                    completedTourIds: Array.from(new Set(currentCompletedTours.concat(tourId)))
                })
            });
        case Constants.UPDATE_CONFIG:
            const { configChange } = action.payload;
            const newConfig = Object.assign({}, state.config, configChange);
            return Object.assign({}, state, {
                config: newConfig
            });
        default:
            return state;
    }
}
