import * as messenger from '../core/utils/messenger';
import { GET_FIGMA_FILE_INFO, SET_RELAUNCH_DATA_ON_ALL_TEXTS, LISTEN_SELECTION_CHANGE, EXPORT_TO_WEB_APP, GET_TEXT_COMPONENTS_VALUE, SET_TEXT_COMPONENTS_VALUE, SELECT_COMPONENT, SET_SESSION_IN_LOCAL_STORAGE, GET_SESSION_FROM_LOCAL_STORAGE, CLEAR_SESSION_FROM_LOCAL_STORAGE, SET_CLIENT_STORAGE, GET_CLIENT_STORAGE } from "../core/constants/PluginMessageTypes";
import * as copy from "copy-to-clipboard";
import * as ProjectOriginTypes from "../core/constants/ProjectOriginTypes";
class FigmaPlatformAPI {
    getPlatformTerminology() {
        return {
            displayName: 'Figma',
            frameDisplayName: 'Frame',
            componentDisplayName: 'Component'
        };
    }
    async getPlatformIdentifiers() {
        return { id: ProjectOriginTypes.FIGMA };
    }
    getPlatformSettings() {
        return { isUploadRawSvg: false, isSupportsReactiveActions: true, isPersistSessionUsingLocalStorage: true };
    }
    async getFileInfo() {
        const figmaFileInfo = await messenger.callFigma(GET_FIGMA_FILE_INFO);
        return figmaFileInfo;
    }
    copyToClipboard(text) {
        copy(text);
    }
    openExternalURL(url) {
        window.open(url);
    }
    onInit() {
        parent.postMessage({ pluginMessage: { type: SET_RELAUNCH_DATA_ON_ALL_TEXTS } }, '*');
    }
    addSelectionChangeListener(listenerId, filterType, callback) {
        messenger.setMessageHandler(`${LISTEN_SELECTION_CHANGE}_${listenerId}`, callback);
        parent.postMessage({ pluginMessage: { type: LISTEN_SELECTION_CHANGE, listenerId, filterType } }, '*');
    }
    removeSelectionChangeListener(listenerId) {
        messenger.removeMessageHandler(`${LISTEN_SELECTION_CHANGE}_${listenerId}`);
    }
    async exportToWebApp(selectedFrames) {
        return messenger.callFigma(EXPORT_TO_WEB_APP, { selectedFrames }, 1000 * 60 * 5);
    }
    async getTextComponentsValue(componentsIds) {
        return messenger.callFigma(GET_TEXT_COMPONENTS_VALUE, { componentsIds });
    }
    async setTextComponentsValue(components) {
        return messenger.callFigma(SET_TEXT_COMPONENTS_VALUE, { components }, 1000 * 300);
    }
    async runInEditContext(asyncFunc) {
        return asyncFunc();
    }
    selectComponent(componentId) {
        parent.postMessage({ pluginMessage: { type: SELECT_COMPONENT, componentId } }, "*");
    }
    async setLocalStorage(key, value) {
        return messenger.callFigma(SET_CLIENT_STORAGE, { key, value });
    }
    async getLocalStorage(key) {
        return messenger.callFigma(GET_CLIENT_STORAGE, { key });
    }
    async setSessionInLocalStorage(sessionId) {
        return messenger.callFigma(SET_SESSION_IN_LOCAL_STORAGE, { sessionId });
    }
    async getSessionFromLocalStorage() {
        return messenger.callFigma(GET_SESSION_FROM_LOCAL_STORAGE);
    }
    async clearSessionFromLocalStorage() {
        return messenger.callFigma(CLEAR_SESSION_FROM_LOCAL_STORAGE);
    }
}
export const figmaPlatformAPI = new FigmaPlatformAPI();
