import { Constants } from './constants';
import { indexExportedFramesByNodeId, indexManagedTextsByFigmaTextId } from "../../utils/text";
const initialState = {
    currentProjectId: "",
    currentFigmaFileName: "",
    exportedFrames: {},
    managedTexts: {},
    exportProcessStatus: "initial",
    exportProcessExtraData: null,
    workspaceTags: []
};
export function textReducer(state = initialState, action) {
    switch (action.type) {
        case Constants.SET_MANAGED_TEXTS:
            const figmaTextIdToNewManagedText = indexManagedTextsByFigmaTextId(action.payload.newManagedTexts);
            const updatedManagedTexts = Object.assign({}, state.managedTexts, figmaTextIdToNewManagedText);
            return Object.assign({}, state, {
                managedTexts: updatedManagedTexts
            });
        case Constants.ADD_EXPORTED_FRAMES:
            const nodeIdToNewExportedFrame = indexExportedFramesByNodeId(action.payload.newFrames);
            const updatedExportedFrames = Object.assign({}, state.exportedFrames, nodeIdToNewExportedFrame);
            return Object.assign({}, state, {
                exportedFrames: updatedExportedFrames
            });
        case Constants.ADD_TEXT_CHANGE:
            const { change, figmaTextId } = action.payload;
            const managedText = Object.assign({}, state.managedTexts[figmaTextId]);
            const newHistory = [change, ...managedText.history];
            managedText.history = newHistory;
            managedText.lastChange = change.value ? change : managedText.lastChange;
            const newManagedTexts = Object.assign({}, state.managedTexts, {
                [figmaTextId]: managedText
            });
            return Object.assign({}, state, {
                managedTexts: newManagedTexts
            });
        case Constants.SET_STATE:
            const newState = action.payload;
            const newTextState = Object.assign({}, newState.text);
            const { managedTexts, exportedFrames } = newTextState;
            return Object.assign({}, state, newTextState, {
                managedTexts: indexManagedTextsByFigmaTextId(managedTexts),
                exportedFrames: indexExportedFramesByNodeId(exportedFrames)
            });
        case Constants.SET_EXPORT_PROCESS_STATUS:
            const { status, extra } = action.payload;
            return Object.assign({}, state, {
                exportProcessStatus: status,
                exportProcessExtraData: extra
            });
        case Constants.CREATE_NEW_TAG:
            const { tag } = action.payload;
            const { workspaceTags } = state;
            return Object.assign({}, state, {
                workspaceTags: [...workspaceTags, tag]
            });
        case Constants.UPDATE_TEXT:
            const { managedText: managedTextToUpdate, properties } = action.payload;
            const updatedTextFigmaId = Object.keys(state.managedTexts).find((textFigmaId) => {
                const managedText = state.managedTexts[textFigmaId];
                return managedText && managedText.id === managedTextToUpdate.id;
            });
            const managedTextsWithUpdatedText = Object.assign({}, state.managedTexts, {
                [updatedTextFigmaId]: Object.assign({}, managedTextToUpdate, properties)
            });
            return Object.assign({}, state, {
                managedTexts: managedTextsWithUpdatedText
            });
        case Constants.SET_CURRENT_PROJECT_ID:
            const { projectId } = action.payload;
            return Object.assign({}, state, {
                currentProjectId: projectId
            });
        case Constants.UPDATE_HISTORY_ENTRY:
            const { textId, entryId, updates } = action.payload;
            const [textToUpdateFigmaId, managedTextToUpdateHistory] = Object.entries(state.managedTexts).find(([figmaId, { id }]) => id === textId);
            if (!managedTextToUpdateHistory) {
                return state;
            }
            const updatedHistory = managedTextToUpdateHistory.history.map((entry) => {
                const { createdAt } = entry;
                if (createdAt === entryId) {
                    return Object.assign({}, entry, updates);
                }
                return entry;
            });
            const managedTextsWithUpdatedTextHistory = Object.assign({}, state.managedTexts, {
                [textToUpdateFigmaId]: Object.assign({}, managedTextToUpdateHistory, {
                    history: updatedHistory
                })
            });
            return Object.assign({}, state, {
                managedTexts: managedTextsWithUpdatedTextHistory
            });
        default:
            return state;
    }
}
