import { scenegraph, application, storage } from '../sdk';
const fs = storage.localFileSystem;
import { getNodeType, isNodeVisible, treeDeepSearch, getNodePositionWithinArtboard, hasMixedStyle, getTextStyleRangeAtIndex, isNodeExportable } from './utils/xd';
import { XD_PAGE_DEFAULT_ID, XD_PAGE_DEFAULT_NAME } from '../../config';
import { detectFirstCharacterIndex } from "../../core/utils/misc";
import { generateRandomString } from '../../core/utils/misc';
async function exportSvgRendition(node) {
    const tempFolder = await fs.getTemporaryFolder();
    const outputFile = await tempFolder.createFile(generateRandomString(6), { overwrite: true });
    await application.createRenditions([{
            node,
            outputFile,
            type: 'svg',
            minify: true,
            embedImages: true
        }]);
    const arrayBuffer = await outputFile.read({ format: storage.formats.binary });
    return new Uint8Array(arrayBuffer);
}
function getTextDecorationLine(textNode) {
    const isAllUnderline = textNode.styleRanges.every(style => style.underline);
    const isAllStrikethrough = textNode.styleRanges.every(style => style.strikethrough);
    if (isAllUnderline) {
        return 'underline';
    }
    if (isAllStrikethrough) {
        return 'line-through';
    }
    return null;
}
function getTextCase(styleRange) {
    let { textTransform } = styleRange;
    switch (textTransform) {
        case 'uppercase':
            return 'uppercase';
        case 'lowercase':
            return 'lowercase';
        case 'titlecase':
            return 'capitalize';
        default:
            return undefined;
    }
}
function getFontDetails(textNode, firstCharacterIndex = 0) {
    const WEIGHT_MAP = {
        "extra light": 200,
        "semi bold": 600,
        "extra bold": 800,
        "thin": 100,
        "extralight": 200,
        "light": 300,
        "regular": 400,
        "medium": 500,
        "semibold": 600,
        "bold": 700,
        "extrabold": 800,
        "black": 900,
        "solid": 900 // found it under 'Font Awesome 5 Free'
    };
    let { fontFamily, fontStyle: styleDescription } = getTextStyleRangeAtIndex(textNode, firstCharacterIndex);
    styleDescription = styleDescription.replace(/-/g, '').toLowerCase();
    const weightOptions = Object.keys(WEIGHT_MAP).filter(weight => styleDescription.indexOf(weight) !== -1);
    // if (weightOptions.length !== 1) { //TODO log
    //     figmaSandboxLogger.error(new Error('weightOptions is not decisive'), {
    //         handlerType: 'figma sandbox: export to web app',
    //         fontFamily,
    //         styleDescription,
    //         weightOptions
    //     })
    // }
    const fontDetails = {
        fontFamily,
        fontWeight: WEIGHT_MAP[weightOptions[0] || 'regular'].toString()
    };
    if (styleDescription.indexOf('italic') !== -1) {
        Object.assign(fontDetails, { fontStyle: 'italic' });
    }
    return fontDetails;
}
export function getAreaBoxProperties(textNode) {
    const { areaBox, layoutBox, localBounds } = textNode;
    const dimensions = { width: localBounds.width, height: localBounds.height };
    let textBoxStrech, overflowY, wordBreak;
    if (layoutBox) { // since XD 34
        const { type, width, height } = layoutBox;
        switch (type) {
            case scenegraph.Text.POINT:
                textBoxStrech = 'horizontal';
                break;
            case scenegraph.Text.FIXED_HEIGHT:
                textBoxStrech = 'vertical';
                overflowY = 'hidden';
                wordBreak = 'break-word';
                break;
            case scenegraph.Text.AUTO_HEIGHT:
                textBoxStrech = 'vertical';
                wordBreak = 'break-word';
                break;
            default:
                textBoxStrech = 'vertical';
        }
    }
    else { // deprecated on XD 34, supporting for backward compatibility
        if (areaBox) {
            textBoxStrech = 'vertical';
            overflowY = 'hidden';
        }
        else { // Point Text type
            textBoxStrech = 'horizontal';
        }
    }
    return Object.assign({
        dimensions,
        textBoxStrech
    }, overflowY ? { overflowY } : {}, wordBreak ? { wordBreak } : {});
}
function getTextAlignHorizontal(textNode) {
    switch (textNode.textAlign.toUpperCase()) {
        case 'ALIGN_LEFT':
        case 'LEFT':
            return 'left';
        case 'ALIGN_CENTER':
        case 'CENTER':
            return 'center';
        case 'ALIGN_RIGHT':
        case 'RIGHT':
            return 'right';
        default:
            return 'left';
    }
}
async function getArtboardTexts(artboard) {
    const artboardTextNodes = treeDeepSearch([artboard], node => getNodeType(node) === 'Text' && isNodeVisible(node));
    const texts = [];
    for (const artboardTextNode of artboardTextNodes) {
        const nodePositionWithinArtboard = getNodePositionWithinArtboard(artboardTextNode, artboard);
        if (!nodePositionWithinArtboard || artboardTextNode.text.length === 0) {
            continue;
        }
        let firstCharacterIndex = detectFirstCharacterIndex(artboardTextNode.text);
        if (firstCharacterIndex === -1) {
            firstCharacterIndex = 0;
        }
        const firstCharacterStyleRange = getTextStyleRangeAtIndex(artboardTextNode, firstCharacterIndex);
        const properties = {
            position: nodePositionWithinArtboard,
            textAlignHorizontal: getTextAlignHorizontal(artboardTextNode),
            textAlignVertical: 'top',
            fontSize: firstCharacterStyleRange.fontSize,
            color: firstCharacterStyleRange.fill.toRgba()
        };
        Object.assign(properties, getAreaBoxProperties(artboardTextNode));
        const isMixedStyle = hasMixedStyle(artboardTextNode);
        isMixedStyle && Object.assign(properties, { isMixedStyle });
        const opacity = artboardTextNode.opacity;
        opacity < 1 && Object.assign(properties, { opacity });
        Object.assign(properties, getFontDetails(artboardTextNode, firstCharacterIndex));
        const lineHeightObj = artboardTextNode.lineSpacing === 0 ? null : { value: artboardTextNode.lineSpacing, unit: 'pixels' };
        lineHeightObj && Object.assign(properties, { lineHeightObj });
        const letterSpacing = (typeof firstCharacterStyleRange.charSpacing === 'number' && firstCharacterStyleRange.charSpacing !== 0) ?
            `${(firstCharacterStyleRange.charSpacing / 1000).toFixed(3)}em` : '';
        letterSpacing && Object.assign(properties, { letterSpacing });
        const textCase = getTextCase(firstCharacterStyleRange);
        textCase && Object.assign(properties, { textCase });
        const textDecorationLine = getTextDecorationLine(artboardTextNode);
        textDecorationLine && Object.assign(properties, { textDecorationLine });
        texts.push({
            id: artboardTextNode.guid,
            value: artboardTextNode.text,
            properties
        });
    }
    return texts;
}
export const exportToWebApp = async (selectedFrames) => {
    const selectedFrameIds = Object.keys(selectedFrames);
    const exports = [];
    for (let i = 0; i < scenegraph.root.children.length; i++) {
        const node = scenegraph.root.children.at(i);
        if (!isNodeExportable(node) ||
            (selectedFrameIds.length === 0 && !isNodeVisible(node)) ||
            (selectedFrameIds.length > 0 && selectedFrameIds.indexOf(node.guid) === -1)) {
            continue;
        }
        // figmaSandboxLogger.info('started collecting texts')
        const texts = await getArtboardTexts(node);
        if (texts.length === 0) {
            continue;
        }
        // figmaSandboxLogger.info('finished collecting texts')
        const exportedSvgUint8Array = await exportSvgRendition(node);
        // figmaSandboxLogger.info('finished exporting SVG')
        exports.push({
            id: node.guid,
            name: node.name,
            type: node.type,
            parentPage: {
                id: XD_PAGE_DEFAULT_ID,
                name: XD_PAGE_DEFAULT_NAME
            },
            exportedSvgUint8Array,
            properties: {
                dimensions: { width: node.width, height: node.height } // height - For scrollable Artboards, this is the total height encompassing all content - not just the viewport size (i.e. screen height).
            },
            texts,
            selectedTextIds: selectedFrames[node.guid].selectedTextIds,
            clipsContent: true // A Figma originated property, default to true to keep things as worked before
        });
    }
    return exports;
};
