import { Constants } from './constants';
import { FRONTITUDE_WEB, GOOGLE_SHEETS } from "../../constants/IntegrationTypes";
const initialState = {
    [FRONTITUDE_WEB]: {
        lastPulledAt: null,
        lastPulledChanges: [],
        isPullingChangesInProgress: false,
        selection: []
    }
};
export function integrationsReducer(state = initialState, action) {
    switch (action.type) {
        case Constants.SET_STATE:
            const newState = action.payload;
            const integrationsState = newState.integrations;
            return Object.assign({}, state, integrationsState);
        case Constants.SET_GOOGLE_SHEETS_PULLED_CHANGES:
            const newGoogleSheetsState = Object.assign({}, state[GOOGLE_SHEETS], {
                lastPulledChanges: action.payload.lastPulledChanges,
                lastPulledAt: new Date()
            });
            return Object.assign({}, state, {
                [GOOGLE_SHEETS]: newGoogleSheetsState
            });
        case Constants.SET_FRONTITUDE_PULLED_CHANGES:
            const newFrontitudeState = Object.assign({}, state[FRONTITUDE_WEB], {
                lastPulledChanges: action.payload.lastPulledChanges,
                lastPulledAt: new Date(),
                selection: action.payload.selection
            });
            return Object.assign({}, state, {
                [FRONTITUDE_WEB]: newFrontitudeState
            });
        case Constants.CONNECT_GOOGLE_SHEET:
            const connectedSheet = action.payload;
            return Object.assign({}, state, {
                [GOOGLE_SHEETS]: connectedSheet
            });
        case Constants.DISCONNECT_GOOGLE_SHEET:
            return Object.assign({}, state, {
                [GOOGLE_SHEETS]: null
            });
        default:
            return state;
    }
}
