import fetch from './fetch-gw';
import { fetchPolling } from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function persistSessionCookiePolling(sessionId, isRespondWithAssociatedSessionId = false) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ sessionId, isRespondWithAssociatedSessionId })
    };
    return fetchPolling(`${FRONTEND_HOSTNAME}/auth/persist-session`, params);
}
export function logout() {
    const params = {
        method: 'GET',
        credentials: 'include',
        type: 'text'
    };
    return fetch(`${FRONTEND_HOSTNAME}/auth/logout`, params);
}
