import { action } from 'typesafe-actions';
import { Constants } from './constants';
import * as textApi from '../../../../commons/api/text';
import { createUploadGateway, uploadFileDirectlyToS3 } from '../../../../commons/api/designPreviewsStorage';
import { handleDesignToolFrameExport } from '../../../../commons/api/projects';
import { getFrameSvgWithoutTexts } from "../../utils/export";
import { convertSvgToJpegImage } from "../../utils/dataTypes";
import * as logger from "../../../../commons/utils/logger";
import { promptBell } from "../app/actions";
import { platformAPI } from "../../platformAPI";
import async from "async-es";
import { isExportableType } from "../../utils/figma";
import * as analytics from "../../../../commons/utils/analytics";
import { generateRandomString } from "../../utils/misc";
export const submitTextChange = ({ note, text, figmaTextId, linkedTextIds = [] }) => {
    return async (dispatch, getState) => {
        const store = getState();
        const { app: appState, text: textState } = store;
        const { user } = appState;
        const { managedTexts } = textState;
        const currentManagedText = managedTexts[figmaTextId];
        const isComment = !text;
        const platformIdentifiers = await platformAPI.getPlatformIdentifiers();
        const response = await textApi.submitTextChange({
            textId: currentManagedText.id,
            note,
            text,
            textSourceId: platformIdentifiers.id
        });
        const linkedTextsUpdates = linkedTextIds.map((nodeId) => ({
            id: nodeId,
            value: text
        }));
        linkedTextsUpdates.forEach(({ id: nodeId }) => {
            const managedText = managedTexts[nodeId];
            if (!managedText) {
                return;
            }
            dispatch(action(Constants.UPDATE_TEXT, {
                managedText,
                properties: {
                    lastChange: Object.assign({}, managedText.lastChange, {
                        value: text
                    })
                }
            }));
        });
        await platformAPI.setTextComponentsValue(linkedTextsUpdates);
        const textChange = Object.assign({
            textId: currentManagedText.id,
            note,
            value: text,
            createdAt: response.createdAt,
            textSourceId: platformIdentifiers.id,
            author: user
        }, !isComment ? {
            previousValue: currentManagedText.lastChange.value
        } : {});
        return dispatch(addTextChange({
            figmaTextId,
            change: textChange
        }));
    };
};
function normalizeExportedFrameVisuals(frame) {
    if (platformAPI.getPlatformTerminology().displayName === 'Sketch' &&
        Object.prototype.toString.call(frame.exportedSvgUint8Array) === "[object Object]") // somehow Sketch returns a generic object, not Uint8Array
     {
        frame.exportedSvgUint8Array = Uint8Array.from(Object.keys(frame.exportedSvgUint8Array).map((key, index) => frame.exportedSvgUint8Array[index]));
        frame.exportedThumbnailUint8Array = Uint8Array.from(Object.keys(frame.exportedThumbnailUint8Array).map((key, index) => frame.exportedThumbnailUint8Array[index]));
    }
}
export const exportComponents = (components) => {
    logger.info('Starting the export process', { components });
    return async (dispatch, getState) => {
        const store = getState();
        const { text, app } = store;
        const { isOnboardingMode } = app;
        const { currentProjectId } = text;
        const figmaFileInfo = await platformAPI.getFileInfo();
        const newManagedTexts = [];
        const platformIdentifiers = await platformAPI.getPlatformIdentifiers();
        const framesToExport = {};
        components.forEach((node) => {
            const { nodeId, path, type } = node;
            const frameId = isExportableType(type) ? nodeId : path[1].id;
            if (!framesToExport.hasOwnProperty(frameId)) {
                framesToExport[frameId] = { selectedTextIds: [] };
            }
            if (type === 'TEXT') {
                framesToExport[frameId].selectedTextIds.push(nodeId);
            }
        });
        logger.info('Collected frames to export', { framesToExport });
        try {
            const frameIdsToExport = Object.keys(framesToExport);
            // TODO: check quota from server
            dispatch(setExportProcessStatus("in-progress", {
                completed: 0,
                total: frameIdsToExport.length,
                stage: "text-extract"
            }));
            if (frameIdsToExport.length === 0) {
                throw new Error('exportComponents: no frames given');
            }
            logger.info('Collected frame IDs to export', { frameIdsToExport });
            const { isUploadRawSvg, isConvertExportedSvgInClient } = platformAPI.getPlatformSettings();
            async.eachOfLimit(frameIdsToExport, 1, async (frameId, index) => {
                const exportFrame_Start = Date.now();
                try {
                    dispatch(setExportProcessStatus("in-progress", {
                        completed: index,
                        total: frameIdsToExport.length,
                        stage: "text-extract"
                    }));
                    logger.info('Calling Figma to get frame export information', { frameId });
                    const frameExports = await platformAPI.exportToWebApp({ [frameId]: framesToExport[frameId] });
                    logger.info('Received export information from Figma', { frameExports });
                    const isFrameExportsHasNoText = !frameExports || frameExports.length === 0;
                    if (isFrameExportsHasNoText) {
                        logger.info('Frame has no text, nothing to do for this frame', { frameId, frameExports });
                        return;
                    }
                    const fileName = generateRandomString(8);
                    const frame = frameExports[0];
                    dispatch(setExportProcessStatus("in-progress", {
                        completed: index,
                        total: frameIdsToExport.length,
                        stage: "image-upload"
                    }));
                    normalizeExportedFrameVisuals(frame);
                    if (isUploadRawSvg) {
                        frame.s3DesignRawSvgFilePath = await uploadSvgToS3(frame, frame.exportedSvgUint8Array, fileName, { format: "svg" });
                    }
                    else {
                        if (isConvertExportedSvgInClient) {
                            const frameSvg = getFrameSvgWithoutTexts(frame.exportedSvgUint8Array);
                            const imageBlob = await convertSvgToJpegImage(frameSvg, frame.properties.dimensions.width, frame.properties.dimensions.height, { returnedValue: 'Blob' });
                            frame.s3DesignFilePath = await uploadSvgToS3(frame, imageBlob, `${fileName}-stripped`, { format: "jpg" });
                        }
                        else {
                            frame.s3DesignFilePath = await uploadSvgToS3(frame, frame.exportedSvgUint8Array, `${fileName}-stripped`, { format: "png" });
                        }
                        frame.s3ThumbnailFilePath = await uploadSvgToS3(frame, frame.exportedThumbnailUint8Array, `${fileName}-thumbnail`, { format: "png" });
                    }
                    logger.info('Image uploading to S3 finished successfully', { frameName: frame.name, frameId: frame.id });
                    delete frame['exportedSvgUint8Array'];
                    delete frame['exportedThumbnailUint8Array'];
                    const { frameManagedTexts, projectId } = await handleDesignToolFrameExport(platformIdentifiers.id, currentProjectId, frame, figmaFileInfo.id, figmaFileInfo.name);
                    dispatch(setCurrentProjectId(projectId));
                    newManagedTexts.push(...frameManagedTexts);
                    logger.info("Frame exported successfully", { frameName: frame.name, frameId: frame.id });
                    analytics.track("Frame exported successfully", {
                        frameName: frame.name,
                        frameId: frame.id,
                        durationSeconds: (Date.now() - exportFrame_Start) / 1000
                    });
                }
                catch (error) {
                    if (error && error.name === 'HTTP_CODE_402') {
                        dispatch(setExportProcessStatus("frames-limit-reached"));
                        throw error;
                    }
                    if (error && error.name === 'HTTP_CODE_405') {
                        dispatch(setExportProcessStatus("projects-limit-reached"));
                        throw error;
                    }
                    logger.error(error, { action: "Export to Frontitude", frames: components });
                    dispatch(setExportProcessStatus("failed"));
                    throw error;
                }
            }, (error) => {
                if (error) {
                    return;
                }
                if (newManagedTexts.length === 0) {
                    dispatch(setExportProcessStatus("no-texts-selected"));
                    return;
                }
                dispatch(setExportProcessStatus("in-progress", {
                    completed: frameIdsToExport.length,
                    total: frameIdsToExport.length,
                    stage: "complete"
                }));
                setTimeout(() => {
                    dispatch(addExportedFrames(components));
                    dispatch(addManagedTexts(newManagedTexts));
                    if (isOnboardingMode) {
                        dispatch(setExportProcessStatus("completed"));
                    }
                    else {
                        dispatch(setExportProcessStatus("initial"));
                    }
                }, 1200);
            });
        }
        catch (error) {
            logger.error(error, { frames: components });
            dispatch(setExportProcessStatus("failed"));
        }
    };
};
export const attachTagToText = ({ managedText, tag }) => {
    return async (dispatch, getState) => {
        const store = getState();
        const { text } = store;
        const { managedTexts } = text;
        const { id: textId } = managedText;
        if (!managedText) {
            throw Error("Could not find managed text to attach a tag");
        }
        const { tagIds: existingTagIds = [] } = managedText;
        const tagIds = [...existingTagIds, tag.id];
        try {
            const { linkedTextIds } = await textApi.updateManagedText({ textId, properties: { tagIds } });
            const textIds = linkedTextIds && linkedTextIds.length > 0 ? linkedTextIds.concat(textId) : [textId];
            const managedTextsArr = Object.keys(managedTexts).map((nodeId) => managedTexts[nodeId]);
            textIds.forEach((textId) => {
                const managedText = managedTextsArr.find(({ id }) => id === textId);
                dispatch(action(Constants.UPDATE_TEXT, {
                    managedText,
                    properties: {
                        tagIds
                    }
                }));
            });
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to add tag to text, please try again or contact us at support@frontitude.com"
            }));
        }
    };
};
export const detachTagFromText = ({ managedText, tag }) => {
    return async (dispatch, getState) => {
        const store = getState();
        const { text } = store;
        const { managedTexts } = text;
        const { id: textId } = managedText;
        if (!managedText) {
            throw Error("Could not find managed text to attach a tag");
        }
        const { tagIds: existingTags } = managedText;
        const tagIds = existingTags.filter((id) => id !== tag.id);
        try {
            const { linkedTextIds } = await textApi.updateManagedText({ textId, properties: { tagIds } });
            const managedTextsArr = Object.keys(managedTexts).map((nodeId) => managedTexts[nodeId]);
            const textIds = linkedTextIds && linkedTextIds.length > 0 ? linkedTextIds.concat(textId) : [textId];
            textIds.forEach((textId) => {
                const managedText = managedTextsArr.find(({ id }) => id === textId);
                dispatch(action(Constants.UPDATE_TEXT, {
                    managedText,
                    properties: {
                        tagIds
                    }
                }));
            });
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to remove tag from text, please try again or contact us at support@frontitude.com"
            }));
        }
    };
};
export const createNewTag = ({ tagLabel }) => {
    return async (dispatch) => {
        try {
            const newTag = await textApi.createNewTag({ tagLabel });
            dispatch(action(Constants.CREATE_NEW_TAG, { tag: newTag }));
            return newTag;
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to create tag, please try again or contact us at support@frontitude.com"
            }));
        }
    };
};
export const addTextChange = ({ figmaTextId, change }) => {
    const { textId, note, value, previousValue, createdAt, textSourceId, author } = change;
    return action(Constants.ADD_TEXT_CHANGE, {
        figmaTextId,
        change: {
            textId,
            note,
            value,
            previousValue,
            createdAt,
            textSourceId,
            author
        }
    });
};
export const setExportProcessStatus = (status, extra = {}) => {
    return action(Constants.SET_EXPORT_PROCESS_STATUS, { status, extra });
};
export const updateTextHistoryEntry = ({ textId, entryId, updates }) => {
    return async (dispatch) => {
        try {
            await textApi.updateTextHistoryEntry({ textId, entryId, updates });
            dispatch(action(Constants.UPDATE_HISTORY_ENTRY, { textId, entryId, updates }));
        }
        catch (error) {
            logger.error(error);
            throw error;
        }
    };
};
const addManagedTexts = (newManagedTexts) => {
    return action(Constants.SET_MANAGED_TEXTS, { newManagedTexts });
};
const addExportedFrames = (newFrames) => {
    return action(Constants.ADD_EXPORTED_FRAMES, { newFrames });
};
const setCurrentProjectId = (projectId) => {
    return action(Constants.SET_CURRENT_PROJECT_ID, { projectId });
};
const uploadSvgToS3 = async (frame, svg, fileName, { format }) => {
    const { gatewayDetails } = await createUploadGateway();
    if (format === "svg") {
        const { fullPath } = await uploadFileDirectlyToS3(gatewayDetails, svg.buffer, {
            overrideFilename: `${fileName}.svg`,
            overrideFileType: 'image/svg+xml;charset=utf-8'
        });
        return fullPath;
    }
    const svgBlob = (svg instanceof Blob) ? svg : new Blob([svg], { type: `image/${format}` });
    const { fullPath } = await uploadFileDirectlyToS3(gatewayDetails, svgBlob, { overrideFilename: `${fileName}.${format}` });
    return fullPath;
};
