import fetch from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function createUploadGateway() {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({})
    };
    return fetch(`${FRONTEND_HOSTNAME}/design_previews_storage/create_upload_gateway`, params);
}
/**
 * @note file is shown in the bucket only after upload finish.
 *
 * @param gatewayDetails. the outcome of createUploadGateway function.
 * @param file. File object. i.e. inputFileElement.files[i]
 * @param progressHandlerFn(loaded, total).
 *
 * @return file remote path on complete
 */
export function uploadFileDirectlyToS3(gatewayDetails, file, { overrideFilename, overrideFileType }, progressHandlerFn = (loaded, total) => { }) {
    const filename = overrideFilename || file.name;
    gatewayDetails.fields.key = gatewayDetails.fields.key + filename; // re-set key which is initiated to hold the path without the file name
    const fullPath = gatewayDetails.fields.key;
    let formData = new FormData();
    for (let gatewayDetailsKey in gatewayDetails.fields) {
        formData.append(gatewayDetailsKey, gatewayDetails.fields[gatewayDetailsKey]);
    }
    formData.append('Content-Type', overrideFileType || file.type);
    formData.append('acl', 'private');
    /* @note: AWS ignores all fields in the request after the file field, so all other fields must appear before the file */
    formData.append('file', file, filename);
    return new Promise((resolve, reject) => {
        let request = new XMLHttpRequest();
        request.open('POST', gatewayDetails.url);
        request.onreadystatechange = function () {
            if (request.readyState > 3) {
                if (request.status >= 200 && request.status < 300) {
                    resolve({
                        url: 'https://s3-eu-west-1.amazonaws.com/' + fullPath,
                        fullPath
                    });
                }
                else {
                    reject(new Error(`request rejected. status: ${request.status}. message: ${request.responseText}`));
                }
            }
        };
        request.onerror = function () {
            reject(new Error(`network level rejected. status: ${request.status}. message: ${request.responseText}`));
        };
        request.upload.onprogress = function (event) {
            progressHandlerFn(event.loaded, event.total);
        };
        request.send(formData);
    });
}
