import { combineReducers, createStore, applyMiddleware } from 'redux';
import { connectRouter, routerMiddleware } from 'connected-react-router';
import thunk from 'redux-thunk';
import { appReducer } from './app/reducer';
import { textReducer } from './text/reducer';
import { integrationsReducer } from "./integrations/reducer";
import { discussionsReducer } from "./discussions/reducer";
import pluginRouterMiddleware from "../middleware/router";
import reactiveActionsPublisher from "../middleware/reactiveActionsPublisher";
export function configureStore(history, preloadedState = {}) {
    const store = createStore(combineReducers({
        router: connectRouter(history),
        app: appReducer,
        text: textReducer,
        integrations: integrationsReducer,
        discussions: discussionsReducer
    }), preloadedState, applyMiddleware(thunk, routerMiddleware(history), pluginRouterMiddleware, reactiveActionsPublisher));
    return store;
}
