import fetch from "./fetch-gw";
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function addCompletedTour({ tourId }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ tourId })
    };
    return fetch(`${FRONTEND_HOSTNAME}/user/add_completed_tour`, params);
}
