import { getNodesByGUIDs, getTopParent, isNodeVisible, treeDeepSearch } from './utils/xd';
import { scenegraph } from '../sdk';
import { getNodesOverlappingAreaSize } from '../../core/utils/designTool';
const setNodeTextValue = (node, value) => {
    const designerHints = [];
    const overlapFilterFn = (nodeToFilter) => (nodeToFilter.children.length === 0 && //leaf
        isNodeVisible(nodeToFilter) &&
        getNodesOverlappingAreaSize(node.globalBounds, nodeToFilter.globalBounds) > 0);
    const nodesOverlapCountBefore = treeDeepSearch([getTopParent(node)], overlapFilterFn).length;
    node.text = value;
    const nodesOverlapCountAfter = treeDeepSearch([getTopParent(node)], overlapFilterFn).length;
    if (nodesOverlapCountBefore < nodesOverlapCountAfter) {
        designerHints.push("element-size-change");
    }
    if (node.clippedByArea) {
        designerHints.push("clipped-content");
    }
    return designerHints;
};
export const updateTextComponents = (componentUpdates) => {
    const updatesWithNotes = [];
    if (componentUpdates.length === 0) {
        return updatesWithNotes;
    }
    const nodes = getNodesByGUIDs(componentUpdates.map(({ id }) => id), true);
    componentUpdates.forEach(({ id, value }) => {
        const node = nodes[id];
        if (!node) {
            updatesWithNotes.push({ id, updateError: "non-existent" });
            return;
        }
        if (!scenegraph.selection.isInEditContext(node)) {
            updatesWithNotes.push({ id, updateError: "not-in-edit-context" });
            return;
        }
        const designerHints = setNodeTextValue(node, value);
        if (designerHints.length !== 0) {
            updatesWithNotes.push({ id, designerHints });
            return;
        }
        updatesWithNotes.push({ id });
    });
    return updatesWithNotes;
};
