import * as React from "react";
import * as moment from "moment";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import "./Discussion.scss";
import Avatar from "../../components/Avatar/Avatar";
import { sortByDateProperty } from "../../../../commons/utils/misc";
import TextWithMentions from "../TextWithMentions/TextWithMentions";
import CommentForm from "../CommentForm/CommentForm";
import Button from "../../components/Button/Button";
import Tooltip from "../../components/Tooltip/Tooltip";
import * as DiscussionsActions from "../../store/discussions/actions";
import { getFullName } from "../../../../commons/utils/account";
const INITIAL_VISIBLE_REPLIES_COUNT = 2;
class Discussion extends React.Component {
    static get defaultProps() {
        return {
            isAllowEmptyDiscussionResolve: true
        };
    }
    constructor(props) {
        super(props);
        this.state = {
            isDisplayReplyInput: false,
            visibleCommentsCount: INITIAL_VISIBLE_REPLIES_COUNT
        };
        this.handleResolveToggleClick = this.handleResolveToggleClick.bind(this);
        this.handleCommentSubmit = this.handleCommentSubmit.bind(this);
    }
    async handleResolveToggleClick() {
        const { discussionsActions, discussion, textId } = this.props;
        const { toggleResolvedState } = discussionsActions;
        const { id: discussionId, resolvedBy } = discussion;
        await toggleResolvedState({ textId, discussionId });
        if (resolvedBy) {
            this.setState({
                isDisplayReplyInput: false
            });
        }
        else {
            this.setState({
                visibleCommentsCount: INITIAL_VISIBLE_REPLIES_COUNT
            });
        }
    }
    async handleCommentSubmit() {
        const { discussion } = this.props;
        const { comments = [] } = discussion || {};
        const { visibleCommentsCount } = this.state;
        this.setState({
            isDisplayReplyInput: false,
            visibleCommentsCount: visibleCommentsCount === -1 || visibleCommentsCount >= comments.length ? visibleCommentsCount : visibleCommentsCount + 1
        });
    }
    render() {
        const { textId, textRevisionId, discussion, collaborators, isAllowEmptyDiscussionResolve, onNewDiscussionCreated } = this.props;
        const { isDisplayReplyInput, visibleCommentsCount } = this.state;
        const { id: discussionId, comments, resolvedBy, resolvedAt } = discussion || {};
        const sortedCommentsAsc = discussion ? sortByDateProperty(comments, "createdAt", false) : [];
        const commentsToDisplay = visibleCommentsCount === -1 ? sortedCommentsAsc : sortedCommentsAsc.slice(sortedCommentsAsc.length - visibleCommentsCount);
        const resolvedByCollaborator = resolvedBy ? collaborators.find(({ id }) => id === resolvedBy) : null;
        const isShowResolveToggle = isAllowEmptyDiscussionResolve || (comments && comments.length > 0);
        const CommentReplies = commentsToDisplay.map(({ authorId, message, createdAt }, index) => {
            const author = collaborators.find(({ id }) => id === authorId);
            return React.createElement("div", { key: index, className: "discussion-comment" },
                React.createElement(Avatar, { user: author }),
                React.createElement("div", { className: "comment-content-container" },
                    React.createElement(TextWithMentions, { className: "comment-content" }, message),
                    React.createElement(Tooltip, { position: "top", arrow: true, hideOnClick: false, title: moment(createdAt).format("dddd, MMMM Do [at] h:mm a") },
                        React.createElement("div", { className: "comment-created-at" }, moment(createdAt).fromNow()))));
        });
        return React.createElement("div", { className: "discussion-content" },
            comments && commentsToDisplay.length < comments.length &&
                React.createElement("div", { className: "reply-actions-button view-all-comments", onClick: () => this.setState({ visibleCommentsCount: -1 }) }, "View all comments"),
            CommentReplies,
            resolvedByCollaborator ? React.createElement("div", { className: "resolved-by" },
                React.createElement(Avatar, { user: resolvedByCollaborator }),
                React.createElement("div", { className: "resolver-info" },
                    getFullName(resolvedByCollaborator),
                    " ",
                    React.createElement("span", { className: "resolved-by-message" },
                        "Marked as resolved ",
                        moment(resolvedAt).fromNow()))) : "",
            isDisplayReplyInput && !resolvedByCollaborator &&
                React.createElement(CommentForm, { className: "reply-comment-form", textId: textId, textRevisionId: textRevisionId, discussionId: discussionId, autoFocus: true, isPartOfDiscussion: true, onCommentSubmit: this.handleCommentSubmit, onNewDiscussionCreated: onNewDiscussionCreated }),
            React.createElement("div", { className: "comment-actions" },
                !isDisplayReplyInput && !resolvedByCollaborator &&
                    React.createElement(Button, { className: "comment-action-button reply-button", theme: "clear", onClick: () => this.setState({ isDisplayReplyInput: true }) }, "Reply"),
                !!resolvedBy && isShowResolveToggle &&
                    React.createElement(Button, { className: "comment-action-button", theme: "clear", onClick: this.handleResolveToggleClick }, "Re-open"),
                !resolvedBy && isShowResolveToggle &&
                    React.createElement(Button, { className: "comment-action-button", theme: "clear", onClick: this.handleResolveToggleClick }, "Resolve")));
    }
}
const mapStateToProps = ({}) => {
    return {};
};
const mapDispatchToProps = (dispatch) => {
    return {
        discussionsActions: bindActionCreators(DiscussionsActions, dispatch)
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(Discussion);
