import * as React from "react";
import "./GoogleLoginButton.scss";
import "./GoogleLoginButton.xd.scss";
import GoogleIcon from "../../../../commons/assets/google.svg";
export default class GoogleLoginButton extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { onClick } = this.props;
        return React.createElement("div", { className: "google-login-button", onClick: onClick },
            React.createElement("div", { className: "button-icon" },
                React.createElement(GoogleIcon, null)),
            React.createElement("div", { className: "label" }, "Sign in with Google"));
    }
}
