import * as React from "react";
import { connect } from "react-redux";
import "./MentionInput.scss";
import classNames from "classnames";
import { MentionsInput, Mention } from "react-mentions";
import { isRtl } from "../../../../commons/utils/misc";
import Avatar from "../../components/Avatar/Avatar";
import { getFullName } from "../../../../commons/utils/account";
class MentionInput extends React.Component {
    constructor(props) {
        super(props);
        this.inputRef = null;
        this.inputRef = React.createRef();
        this.renderUserMentionSuggestion = this.renderUserMentionSuggestion.bind(this);
    }
    static get defaultProps() {
        return {
            isSingleLine: false,
            placeholder: "Use '@' to mention collaborators"
        };
    }
    renderUserMentionSuggestion(entry, search, highlightedDisplay, index, focused) {
        const { collaborators } = this.props;
        const { id: collaboratorId } = entry;
        const collaborator = collaborators.find(({ id }) => id === collaboratorId);
        return React.createElement("div", { className: classNames("user-mention-suggestion", { focused }) },
            React.createElement(Avatar, { user: collaborator, size: "small" }),
            highlightedDisplay);
    }
    componentDidMount() {
        const { autoFocus } = this.props;
        autoFocus && this.inputRef.current.focus();
    }
    render() {
        const { className, collaborators, isSingleLine, value, onChange, placeholder, disabled } = this.props;
        const collaboratorsSuggestions = collaborators.map(collaborator => ({ id: collaborator.id, display: getFullName(collaborator) }));
        const isRtlValue = isRtl(value);
        if (disabled) {
            return React.createElement("input", { className: classNames("mention-input", className), type: "text", placeholder: "Attach a note...", disabled: true });
        }
        return React.createElement(MentionsInput, { singleLine: isSingleLine, className: classNames("mention-input", className), value: value, placeholder: placeholder, onChange: onChange, allowSpaceInQuery: true, inputRef: this.inputRef },
            React.createElement(Mention, { trigger: "@", displayTransform: (collaboratorId) => {
                    const collaborator = collaborators.find(({ id }) => id === collaboratorId);
                    return `@${collaborator.firstName}`;
                }, allowSuggestionsAboveCursor: true, markup: "@(__id__)", data: collaboratorsSuggestions, dir: isRtlValue ? "rtl" : "ltr", renderSuggestion: this.renderUserMentionSuggestion, appendSpaceOnAdd: true }));
    }
}
const mapStateToProps = ({ app }) => {
    const { account } = app;
    const { collaborators } = account;
    return {
        collaborators
    };
};
const mapDispatchToProps = (dispatch) => {
    return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(MentionInput);
