import fetch from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function handleDesignToolFrameExport(platformId, projectId, frameExportDetails, designFileId, designFileName) {
    const params = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({
            platformId,
            projectId,
            frameExportDetails,
            designFileId,
            designFileName
        })
    };
    return fetch(`${FRONTEND_HOSTNAME}/projects/handle_design_tool_frame_export`, params);
}
