import { action } from "typesafe-actions";
import { Constants } from "./constants";
import { Constants as TextStoreConstants } from "../text/constants";
import * as integrationsApi from "../../../../commons/api/integrations";
import { promptBell, hideBell, completeProductTour } from "../app/actions";
import { addTextChange } from "../text/actions";
import { SET_TEXT_COMPONENTS_VALUE } from "../../constants/PluginMessageTypes";
import * as logger from "../../../../commons/utils/logger";
import { UPDATES_TAB } from "../../constants/PluginPathTypes";
import { push } from "connected-react-router";
import { platformAPI } from '../../platformAPI';
import { indexManagedTextsByFigmaTextId } from "../../utils/text";
import * as analytics from "../../../../commons/utils/analytics";
export function pullChangesFromGoogleSheets() {
    return async (dispatch, getState) => {
        const store = getState();
        const { app, text } = store;
        const { user } = app;
        const { currentProjectId, managedTexts } = text;
        const newPulledChanges = await integrationsApi.pullChangesFromGoogleSheets(currentProjectId);
        addPulledChangesAsManagedTextChanges({ dispatch, user, managedTexts, pulledChanges: newPulledChanges });
        await updateUnmodifiedTextComponentsValue(managedTexts, newPulledChanges);
        return dispatch(action(Constants.SET_GOOGLE_SHEETS_PULLED_CHANGES, {
            lastPulledChanges: newPulledChanges.map((pulledChange) => Object.assign({}, pulledChange, {
                change: Object.assign({}, pulledChange.change, {
                    author: user
                })
            }))
        }));
    };
}
export function pullChangesFromFrontitude(selection = []) {
    return async (dispatch, getState) => {
        const pullChanges_StartTime = Date.now();
        const store = getState();
        const { app, text } = store;
        const { currentProjectId } = text;
        const { user, config } = app;
        const { completedTourIds } = user;
        const { pullChanges } = config;
        const { textStatus } = pullChanges || {};
        const selectedNodes = selection.map(({ nodeId, type }) => ({ nodeId, type }));
        const newManagedTextsArray = await integrationsApi.pullChangesFromFrontitude({ projectId: currentProjectId, textStatus, selection: selectedNodes });
        const newManagedTexts = indexManagedTextsByFigmaTextId(newManagedTextsArray);
        const modifiedTextComponents = await getModifiedTextComponents(newManagedTexts, selection);
        const lastPulledChanges = modifiedTextComponents
            .map(({ id: figmaTextId, isMixedStyle }) => {
            const managedText = newManagedTexts[figmaTextId];
            const { lastChange } = managedText;
            const { value, createdAt, author } = lastChange;
            isMixedStyle = platformAPI.getPlatformTerminology().displayName !== 'Figma' && isMixedStyle;
            return Object.assign({
                figmaTextId,
                change: {
                    value,
                    createdAt,
                    author
                },
                isMixedStyle
            }, isMixedStyle ? { updateError: "mixed-style" } : {});
        });
        const lastPulledChangesWithMixedStyle = lastPulledChanges.filter(({ isMixedStyle }) => isMixedStyle);
        const lastPulledChangesWithoutMixedStyle = lastPulledChanges.filter(({ isMixedStyle }) => !isMixedStyle);
        const textComponentsUpdates = lastPulledChangesWithoutMixedStyle
            .map(({ figmaTextId, change }) => {
            return {
                id: figmaTextId,
                value: change.value
            };
        });
        const updatePulledTextInFigma_Start = Date.now();
        const updates = await platformAPI.setTextComponentsValue(textComponentsUpdates);
        const updatePulledTextInFigma_Finish = Date.now();
        const platformApiUpdateTimeInSec = (updatePulledTextInFigma_Finish - updatePulledTextInFigma_Start) / 1000;
        const lastPulledUpdates = updates
            .map(({ id, updateError, designerHints }) => {
            const pulledChange = lastPulledChanges.find(({ figmaTextId }) => figmaTextId === id);
            return Object.assign({}, pulledChange, { updateError, designerHints });
        })
            .concat(lastPulledChangesWithMixedStyle);
        dispatch(action(TextStoreConstants.SET_MANAGED_TEXTS, { newManagedTexts: newManagedTextsArray }));
        dispatch(action(Constants.SET_FRONTITUDE_PULLED_CHANGES, { lastPulledChanges: lastPulledUpdates, selection }));
        const pullChanges_EndTime = Date.now();
        const pullChangesTotalTimeInSec = (pullChanges_EndTime - pullChanges_StartTime) / 1000;
        logger.info("Changes pulled", {
            changedTextsCount: textComponentsUpdates.length,
            platformApiUpdateTimeInSec,
            pullChangesTotalTimeInSec
        });
        analytics.track("Changes pulled", {
            changedTextsCount: textComponentsUpdates.length,
            platformApiUpdateTimeInSec,
            pullChangesTotalTimeInSec
        });
        const UPDATES_TAB_PULL_NOTICE = "updates-tab-pull-notice";
        const isPullInfoBoxDismissed = completedTourIds.indexOf(UPDATES_TAB_PULL_NOTICE) > -1;
        if (!isPullInfoBoxDismissed) {
            const onPullInfoClose = async () => {
                dispatch(hideBell());
                await completeProductTour(UPDATES_TAB_PULL_NOTICE)(dispatch);
            };
            promptBell({
                type: "info",
                message: selection && selection.length > 0 ?
                    "<b>Note:</b> Only texts under selection were pulled. In order to pull all texts in file, clear your selection and pull again" :
                    "<b>New!</b> Now you can pull changes solely for texts under selection, by selecting elements (texts, groups, frames, etc.) and pulling again",
                onClose: onPullInfoClose
            })(dispatch);
        }
        return dispatch(push(UPDATES_TAB));
    };
}
function addPulledChangesAsManagedTextChanges({ dispatch, user, managedTexts, pulledChanges }) {
    pulledChanges.forEach(({ figmaTextId, change }) => {
        const currentManagedText = managedTexts[figmaTextId];
        if (!currentManagedText) {
            return;
        }
        const currentManagedTextLastChange = currentManagedText.lastChange;
        const textChange = Object.assign({}, change, currentManagedTextLastChange ? {
            previousValue: currentManagedTextLastChange.value,
        } : {}, user ? { author: user } : {}); //TODO Get the real user from the change in DB. Currently in single player mode it's always the same user
        dispatch(addTextChange({
            figmaTextId,
            change: textChange
        }));
    });
}
async function getModifiedTextComponents(managedTexts, selectedNodes = []) {
    const textComponents = await platformAPI.getTextComponentsValue(Object.keys(managedTexts));
    return textComponents.filter(({ id, value, path }) => {
        const managedText = managedTexts[id];
        const nodeFullPath = path.map(({ id }) => id).concat(id);
        const selectedNodeIds = selectedNodes.map(({ nodeId }) => nodeId);
        const isUnderSelection = (selectedNodeIds.length === 0) || (selectedNodeIds.some(id => nodeFullPath.indexOf(id) > -1));
        return isUnderSelection && value !== managedText.lastChange.value;
    });
}
async function updateUnmodifiedTextComponentsValue(managedTexts, newPulledChanges) {
    if (newPulledChanges.length === 0) {
        return;
    }
    const modifiedTextComponents = await getModifiedTextComponents(managedTexts);
    const modifiedTextComponentsIds = modifiedTextComponents.map(({ id }) => id);
    const unmodifiedFigmaComponentsIdToValue = newPulledChanges
        .filter(({ figmaTextId }) => modifiedTextComponentsIds.indexOf(figmaTextId) === -1)
        .map(({ figmaTextId, change }) => ({ id: figmaTextId, value: change.value }));
    parent.postMessage({ pluginMessage: { type: SET_TEXT_COMPONENTS_VALUE, components: unmodifiedFigmaComponentsIdToValue } }, '*');
}
export function connectGoogleSheet() {
    return async (dispatch, getState) => {
        const store = getState();
        const { text } = store;
        const { currentProjectId } = text;
        try {
            const connectedSheet = await integrationsApi.getConnectedSheetPolling(currentProjectId);
            const { sheetName, sheetUrl, lastPulledAt } = connectedSheet;
            return dispatch(action(Constants.CONNECT_GOOGLE_SHEET, {
                sheetName,
                sheetUrl,
                lastPulledAt,
                lastPulledChanges: []
            }));
        }
        catch (error) {
            logger.error(error, { currentProjectId });
            promptBell({
                type: "error",
                message: "Failed to connect. Please contact us at support@frontitude.com"
            })(dispatch);
        }
    };
}
export function disconnectGoogleSheet() {
    return async (dispatch, getState) => {
        const store = getState();
        const { text } = store;
        const { currentProjectId: projectId } = text;
        try {
            await integrationsApi.disconnectGoogleSheet({ projectId });
            return dispatch(action(Constants.DISCONNECT_GOOGLE_SHEET));
        }
        catch (error) {
            logger.error(error, { projectId });
            promptBell({
                type: "error",
                message: "Failed to remove integration. Please contact us at support@frontitude.com"
            })(dispatch);
        }
    };
}
