import { Constants } from "./constants";
const initialState = {
    discussions: []
};
export function discussionsReducer(state = initialState, action) {
    switch (action.type) {
        case Constants.SET_STATE:
            const newState = action.payload;
            const { discussions: discussionsState } = newState;
            const { discussions } = discussionsState;
            return Object.assign({}, state, {
                discussions
            });
        case Constants.CREATE_DISCUSSION:
            const { discussionId: newDiscussionId, projectId, frameId, textId, textRevisionId } = action.payload;
            const newDiscussion = {
                id: newDiscussionId,
                projectId,
                frameId,
                textId,
                textRevisionId,
                comments: []
            };
            return Object.assign({}, state, {
                discussions: state.discussions.concat(newDiscussion)
            });
        case Constants.SUBMIT_COMMENT:
            const { commentId, discussionId, authorId, message } = action.payload;
            const discussionsWithNewComment = state.discussions.map((discussion) => {
                const { id, comments } = discussion;
                if (id === discussionId) {
                    const newComment = {
                        id: commentId,
                        discussionId,
                        authorId,
                        message,
                        createdAt: new Date(new Date().toUTCString())
                    };
                    return Object.assign({}, discussion, {
                        comments: comments ? comments.concat(newComment) : [newComment]
                    });
                }
                return discussion;
            });
            return Object.assign({}, state, {
                discussions: discussionsWithNewComment
            });
        case Constants.TOGGLE_RESOLVED_STATE:
            const { discussionId: discussionIdToToggle, userId } = action.payload;
            const discussionsWithNewResolvedState = state.discussions.map((discussion) => {
                const { id, resolvedBy } = discussion;
                if (id === discussionIdToToggle) {
                    return Object.assign({}, discussion, {
                        resolvedBy: resolvedBy ? null : userId,
                        resolvedAt: resolvedBy ? null : new Date(new Date().toUTCString())
                    });
                }
                return discussion;
            });
            return Object.assign({}, state, {
                discussions: discussionsWithNewResolvedState
            });
        default:
            return state;
    }
}
