import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { ModalElementId } from '../constants/ModalTypes';
export const showModalDialog = (ReactComponent) => {
    const modalDialogElement = document.getElementById(ModalElementId);
    ReactDOM.render(React.createElement(ReactComponent, null), modalDialogElement);
    modalDialogElement.showModal();
};
