import { ROOT } from "../constants/PluginPathTypes";
export default store => next => action => {
    const { payload } = action;
    if (action.type && payload && action.type.indexOf('@@router/LOCATION_CHANGE') === 0) {
        const { location } = payload;
        const { pathname } = location;
        const apiGwIframe = document.getElementById("api-gw");
        if (pathname !== ROOT && apiGwIframe) {
            apiGwIframe.contentWindow.postMessage({ type: "route-change", pathname }, "*");
        }
    }
    return next(action);
};
