const nativeFetch = window.fetch.bind(window); // note: some versions of edge browser had an error when we didn't use bind. so, yeah, I agree this is weird.. but it is needed.
export default function fetch(url, params) {
    const paramsWrap = Object.assign({}, params, {
        credentials: 'same-origin'
    }, params.type === 'json' ? {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    } : {});
    return nativeFetch(url, paramsWrap)
        .then(response => handleErrorResponse(response))
        .then(response => deserializeResponse(response, paramsWrap.type));
}
export async function handleErrorResponse(response) {
    if (response.status >= 400) {
        const responseText = await response.text();
        throw { message: responseText, code: response.status };
    }
    return response;
}
export function deserializeResponse(response, type) {
    switch (type) {
        case 'json':
            return response.json();
        case 'text':
            return response.text();
        default:
            return response.text();
    }
}
