import { application, uxp, clipboard, scenegraph, viewport, os } from './sdk';
import { handleSelectionChange } from './lib/selection';
import * as exportLib from './lib/export';
import { updateTextComponents } from './lib/text';
import { getNodeType, getNodeByGUID, getNodesByGUIDs, hasMixedStyle, getNodePath } from './lib/utils/xd';
import * as ProjectOriginTypes from "../core/constants/ProjectOriginTypes";
import EditContextNotInRootModal from './lib/components/EditContextNotInRootModal';
import { showModalDialog } from './lib/utils/modal';
import * as analytics from '../../commons/utils/analytics';
const selectionListeners = {};
class XdPlatformAPI {
    getPlatformSettings() {
        return { isUploadRawSvg: true, isUsingNativeTooltips: true, isHideEditTab: true, isSupportsReactiveActions: false };
    }
    getPlatformTerminology() {
        return {
            displayName: 'XD',
            frameDisplayName: 'Artboard',
            componentDisplayName: 'Component'
        };
    }
    async getPlatformIdentifiers() {
        return {
            id: ProjectOriginTypes.XD,
            version: application.version,
            os: (os && typeof os.platform === 'function' && typeof os.release === 'function') ? `${os.platform()} ${os.release()}` : ''
        };
    }
    async getFileInfo() {
        return { id: `xd-${application.activeDocument.guid}`, name: application.activeDocument.name };
    }
    copyToClipboard(text) {
        clipboard.copyText(text);
    }
    openExternalURL(url) {
        uxp.shell.openExternal(url);
    }
    onInit() { }
    addSelectionChangeListener(listenerId, filterType, callback) {
        selectionListeners[listenerId] = () => {
            const selectedComponents = handleSelectionChange(filterType);
            callback({ selectedComponents });
        };
        window.addEventListener('xdSelectionChange', selectionListeners[listenerId], true);
        selectionListeners[listenerId](); // init with current selection
    }
    removeSelectionChangeListener(listenerId) {
        window.removeEventListener('xdSelectionChange', selectionListeners[listenerId]);
        selectionListeners[listenerId] = null;
    }
    async exportToWebApp(selectedFrames) {
        return exportLib.exportToWebApp(selectedFrames);
    }
    async getTextComponentsValue(componentsIds) {
        return getNodesByGUIDs(componentsIds)
            .map((node) => ({
            id: node.guid,
            value: node.text,
            isMixedStyle: hasMixedStyle(node),
            path: getNodePath(node)
        }));
    }
    async setTextComponentsValue(components) {
        return updateTextComponents(components);
    }
    async runInEditContext(asyncFunc, { label = '', isRequestHighestScope = false } = {}) {
        if (isRequestHighestScope && getNodeType(scenegraph.selection.editContext) !== 'RootNode') {
            analytics.track('Encountered modal asking for root selection', { label });
            showModalDialog(EditContextNotInRootModal);
            return;
        }
        return new Promise(async (resolve, reject) => {
            try {
                application.editDocument({ editLabel: label }, async () => {
                    try {
                        const result = await asyncFunc();
                        resolve(result);
                    }
                    catch (editingException) {
                        reject(editingException);
                    }
                });
            }
            catch (contextException) {
                reject(contextException);
            }
        });
    }
    selectComponent(componentId, isZoomToMax = false) {
        this.runInEditContext(() => {
            const node = getNodeByGUID(componentId);
            if (isZoomToMax || viewport.zoomFactor < 0.7) {
                viewport.zoomToRect(node);
            }
            else {
                const { x, y, width } = node.globalDrawBounds;
                const { height: viewportHeight } = viewport.bounds;
                viewport.scrollToCenter(x + width / 2, y + viewportHeight / 5);
            }
        }, { isRequestHighestScope: false });
    }
    async setLocalStorage(key, value) {
        const valueAsString = JSON.stringify(value);
        window.localStorage.setItem(key, valueAsString);
    }
    async getLocalStorage(key) {
        let item = window.localStorage.getItem(key);
        try { // handling non stringified object case
            item = item && JSON.parse(item);
        }
        catch (error) { }
        return item;
    }
    async setSessionInLocalStorage(sessionId) { throw new Error('NOT IMPLEMENTED'); }
    async getSessionFromLocalStorage() { throw new Error('NOT IMPLEMENTED'); return ''; }
    async clearSessionFromLocalStorage() { throw new Error('NOT IMPLEMENTED'); }
}
export const xdPlatformAPI = new XdPlatformAPI();
