import * as React from "react";
import classNames from "classnames";
import "./Avatar.scss";
import AvatarIcon from "../../../../commons/assets/avatar.svg";
import { getNameInitials } from "../../../../commons/utils/account";
export default class Avatar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isAvatarLoadFailed: false
        };
    }
    render() {
        const { user, size } = this.props;
        const { isAvatarLoadFailed } = this.state;
        const { picture } = user || {};
        const nameInitials = getNameInitials(user);
        return React.createElement("div", { className: classNames("avatar", size) },
            picture && !isAvatarLoadFailed ? React.createElement("img", { className: "pic", src: picture, onError: () => this.setState({ isAvatarLoadFailed: true }) }) : nameInitials,
            !user && React.createElement(AvatarIcon, null));
    }
}
