import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { createMemoryHistory } from "history";
import { configureStore } from '../core/store';
import Root from "../core/containers/Root";
import { ModalElementId } from "./lib/constants/ModalTypes";
const history = createMemoryHistory();
const store = configureStore(history);
class PluginPanelController {
    constructor() {
        this.instance = null;
        this.rootNode = document.createElement("div");
        this.rootNode.id = 'root';
        this.xdRootNode = null; // ; is used on purpose. removal mess with next line
        ["show", "hide", "update"].forEach(fn => this[fn] = this[fn].bind(this));
    }
    show(event) {
        if (this.instance) {
            return;
        }
        this.xdRootNode = event.node;
        this.xdRootNode.appendChild(this.rootNode);
        const styleElement = document.createElement("link");
        styleElement.rel = "stylesheet";
        styleElement.href = "./main.style.min.css";
        this.xdRootNode.appendChild(styleElement);
        const dialogElement = document.createElement('dialog');
        dialogElement.id = ModalElementId;
        this.xdRootNode.appendChild(dialogElement);
        this.instance = ReactDOM.render(React.createElement(Root, { store: store, history: history }), this.rootNode);
    }
    hide(event) { }
    update() {
        window.dispatchEvent(new Event('xdSelectionChange'));
    }
}
export default PluginPanelController;
