import * as React from 'react';
import './ProjectActionBar.scss';
import './ProjectActionBar.xd.scss';
import './ProjectActionBar.sketch.scss';
import NewWindowIcon from "../../../../commons/assets/new-window.svg";
import SettingsIcon from "../../../../commons/assets/settings.svg";
import withSelection from "../withSelection";
import SplitButton from "../SplitButton/SplitButton";
import { useState } from "react";
import { initialPullConfig } from "../../store/app/reducer";
class ProjectActionBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isPullChangesLoading: false
        };
        this.handlePullChangesClick = this.handlePullChangesClick.bind(this);
        this.handleConfigTextStatusChange = this.handleConfigTextStatusChange.bind(this);
    }
    handlePullChangesClick() {
        const { selectedComponents, onPullChangesClick } = this.props;
        this.setState({ isPullChangesLoading: true }, async () => {
            await onPullChangesClick(selectedComponents);
            this.setState({ isPullChangesLoading: false });
        });
    }
    handleConfigTextStatusChange(selectedTextStatus) {
        const { onPullConfigChange } = this.props;
        onPullConfigChange({
            textStatus: selectedTextStatus
        });
    }
    render() {
        const { onOpenInFrontitudeClick, pullConfig } = this.props;
        const { isPullChangesLoading } = this.state;
        const { textStatus } = pullConfig || initialPullConfig;
        const isPullConfigChanged = initialPullConfig.textStatus !== textStatus;
        return React.createElement("div", { className: "project-action-bar" },
            React.createElement("div", { className: "open-in-web", onClick: onOpenInFrontitudeClick },
                React.createElement(NewWindowIcon, { className: "icon" }),
                React.createElement("div", null, "Open Frontitude")),
            React.createElement(SplitButton, { className: "pull-changes", isLoading: isPullChangesLoading, Icon: SettingsIcon, onClick: this.handlePullChangesClick, isDotDisplayed: isPullConfigChanged, menuItems: [
                    {
                        MenuItem: () => React.createElement(PullByStatusConfig, { textStatus: textStatus, onTextStatusSelect: this.handleConfigTextStatusChange })
                    }
                ], label: "Pull changes" }));
    }
}
const PullByStatusConfig = ({ textStatus, onTextStatusSelect }) => {
    const [selectedTextStatus, setSelectedTextStatus] = useState(textStatus);
    return React.createElement("div", { className: "button-menu-item pull-by-status-config" },
        React.createElement("div", { className: "label" }, "Pull only copy with status"),
        React.createElement("select", { name: "text-status-select", className: "text-status-select", value: selectedTextStatus || "any", onChange: (e) => {
                const selected = e.target.value === "any" ? null : e.target.value;
                setSelectedTextStatus(selected);
                onTextStatusSelect(selected);
            } },
            React.createElement("option", { value: "new" }, "New"),
            React.createElement("option", { value: "draft" }, "Draft"),
            React.createElement("option", { value: "review" }, "Review"),
            React.createElement("option", { value: "final" }, "Final"),
            React.createElement("option", { value: "any" }, "Any")));
};
export default withSelection(ProjectActionBar, { listenerId: "ProjectActionBar", filterType: "TOP_LEVEL_FRAMES_OR_INNER_TEXTS" });
