import * as React from 'react';
import { diffWords } from 'diff';
import classNames from 'classnames';
import './TextDiff.scss';
export default ({ className, oldText, newText }) => {
    const textDiff = diffWords(oldText, newText);
    return React.createElement("div", { className: classNames("text-diff", className) }, textDiff.map(({ value, added, removed }, index) => React.createElement("span", { key: index, className: classNames({ added, removed }) }, value)));
};
