import fetch from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function initState({ figmaFileId, figmaFileName, platformId }) {
    const params = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ figmaFileId, figmaFileName, platformId })
    };
    return fetch(`${FRONTEND_HOSTNAME}/misc/init`, params);
}
export function requestFeature({ featureName }) {
    const params = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        type: 'text',
        body: JSON.stringify({ featureName })
    };
    return fetch(`${FRONTEND_HOSTNAME}/misc/request_feature`, params);
}
