import { stringToUTF8Array } from "./dataTypes";
import * as analytics from "../../../commons/utils/analytics";
export function getFrameSvgWithoutTexts(frameSvgUint8Array) {
    console.log('started svg texts cleanup');
    const start = Date.now();
    // remove all texts
    let frameSvgUint8String = frameSvgUint8Array.join('_');
    console.log('preparing text tags to search in svg string');
    const textElementOpeningUint8String = stringToUTF8Array('<text').join('_'); // no need to put the suffix '>' since it might have attributes
    const textElementEndingUint8String = stringToUTF8Array('</text>').join('_');
    console.log('preparing regexp for cleaning text tags');
    const regex = new RegExp(`${textElementOpeningUint8String}.*?${textElementEndingUint8String}`, 'gi'); // assuming a text element doesn't exist within another text element
    console.log('text removal from svg string');
    frameSvgUint8String = frameSvgUint8String.replace(regex, ''); // texts removal
    console.log('compost a UInt8 array from cleaned svg string');
    const out = Uint8Array.from(frameSvgUint8String.replace(/(_)\1+/gmi, '_') // remove any consecutive underscore duplicate. might happen after previous regex
        .split('_'), n => parseInt(n));
    console.log('finished svg texts cleanup');
    analytics.track("Texts cleaned from SVG", {
        frameSvgUint8ArrayLength: frameSvgUint8Array.length,
        durationSeconds: (Date.now() - start) / 1000
    });
    return out;
}
