import { PLATFORM } from '../config';
import { xdPlatformAPI } from '../xd/xdPlatformAPI';
import { figmaPlatformAPI } from '../figma/figmaPlatformAPI';
import { sketchPlatformAPI } from '../sketch/sketchPlatformAPI';
class PlatformAPI {
    invoke(func, ...params) {
        switch (PLATFORM) {
            case 'XD':
                return xdPlatformAPI[func](...params);
            case 'SKETCH':
                return sketchPlatformAPI[func](...params);
            default:
                return figmaPlatformAPI[func](...params);
        }
    }
    getPlatformTerminology() {
        return this.invoke('getPlatformTerminology');
    }
    getPlatformIdentifiers() {
        return this.invoke('getPlatformIdentifiers');
    }
    getPlatformSettings() {
        return this.invoke('getPlatformSettings');
    }
    getFileInfo() {
        return this.invoke('getFileInfo');
    }
    copyToClipboard(text) {
        return this.invoke('copyToClipboard', text);
    }
    openExternalURL(url) {
        return this.invoke('openExternalURL', url);
    }
    onInit() {
        return this.invoke('onInit');
    }
    addSelectionChangeListener(listenerId, filterType, callback) {
        return this.invoke('addSelectionChangeListener', listenerId, filterType, callback);
    }
    removeSelectionChangeListener(listenerId) {
        return this.invoke('removeSelectionChangeListener', listenerId);
    }
    async exportToWebApp(selectedFrames) {
        return this.invoke('exportToWebApp', selectedFrames);
    }
    getTextComponentsValue(componentsIds) {
        return this.invoke('getTextComponentsValue', componentsIds);
    }
    async setTextComponentsValue(components) {
        return this.invoke('setTextComponentsValue', components);
    }
    async runInEditContext(asyncFunc, config) {
        return this.invoke('runInEditContext', asyncFunc, config);
    }
    selectComponent(componentId, isZoomToMax) {
        return this.invoke('selectComponent', componentId, isZoomToMax);
    }
    async setLocalStorage(key, value) {
        return this.invoke('setLocalStorage', key, value);
    }
    async getLocalStorage(key) {
        return this.invoke('getLocalStorage', key);
    }
    async setSessionInLocalStorage(sessionId) {
        return this.invoke('setSessionInLocalStorage', sessionId);
    }
    async getSessionFromLocalStorage() {
        return this.invoke('getSessionFromLocalStorage');
    }
    async clearSessionFromLocalStorage() {
        return this.invoke('clearSessionFromLocalStorage');
    }
}
export const platformAPI = new PlatformAPI();
