import fetch from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function submitTextChange({ textId, note, text, textSourceId }) {
    const params = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ textId, note, text, textSourceId, isUpdateDuplicates: true })
    };
    return fetch(`${FRONTEND_HOSTNAME}/text/submit_change`, params);
}
export function updateManagedText({ textId, properties }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ textId, properties })
    };
    return fetch(`${FRONTEND_HOSTNAME}/text/update`, params);
}
export function createNewTag({ tagLabel }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ tagLabel })
    };
    return fetch(`${FRONTEND_HOSTNAME}/text/create_new_tag`, params);
}
export function updateTextHistoryEntry({ textId, entryId, updates }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ textId, entryId, updates })
    };
    return fetch(`${FRONTEND_HOSTNAME}/text/update_history_entry`, params);
}
