import * as ProjectOriginTypes from "../core/constants/ProjectOriginTypes";
import { GET_FIGMA_FILE_INFO, GET_PLATFORM_IDENTIFIERS, OPEN_EXTERNAL_URL, GET_SELECTED_COMPONENTS, SELECT_COMPONENT, SET_TEXT_COMPONENTS_VALUE, GET_TEXT_COMPONENTS_VALUE, EXPORT_TO_WEB_APP, SET_LOCAL_STORAGE, GET_LOCAL_STORAGE } from '../core/constants/PluginMessageTypes';
import * as logger from "../../commons/utils/logger";
import * as copy from "copy-to-clipboard";
const SESSION_ID_NAME = 'ftses';
const selectionListeners = {};
let platformIdentifiers;
//@ts-ignore
window.handleSketchSelectionChange = () => {
    window.dispatchEvent(new Event('sketchSelectionChange'));
};
//@ts-ignore
window.handleSketchSandboxLogs = (payload) => {
    try {
        const { logLevel, logMsg, data } = payload;
        switch (logLevel) {
            case 'error':
                logger.error(new Error(logMsg), data);
                break;
            case 'info':
                logger.info(logMsg, data);
                break;
            default:
                logger.error(new Error('handleSketchSandboxLogs: unknown logLevel'), { payload });
        }
    }
    catch (error) {
        logger.error(error, { payload });
    }
};
class SketchPlatformAPI {
    getPlatformSettings() {
        return { isUploadRawSvg: false, isSupportsReactiveActions: false, isPersistSessionUsingLocalStorage: true, isHideEditTab: true };
    }
    getPlatformTerminology() {
        return {
            displayName: 'Sketch',
            frameDisplayName: 'Artboard',
            componentDisplayName: 'Symbol'
        };
    }
    async getPlatformIdentifiers() {
        if (!platformIdentifiers) {
            //@ts-ignore
            platformIdentifiers = await window.postMessage(GET_PLATFORM_IDENTIFIERS).then(arr => arr[0]);
        }
        return Object.assign({ id: ProjectOriginTypes.SKETCH }, platformIdentifiers);
    }
    async getFileInfo() {
        //@ts-ignore
        return window.postMessage(GET_FIGMA_FILE_INFO).then(arr => arr[0]);
    }
    copyToClipboard(text) {
        copy(text);
    }
    openExternalURL(url) {
        window.postMessage(OPEN_EXTERNAL_URL, url);
    }
    onInit() {
        // handle <a> tag click events at the document level
        document.addEventListener('click', (event) => {
            // @ts-ignore
            const target = event.target.closest('a');
            if (target && target.getAttribute('target') === '_blank') {
                event.preventDefault();
                this.openExternalURL(target.href);
            }
        });
    }
    addSelectionChangeListener(listenerId, filterType, callback) {
        selectionListeners[listenerId] = async () => {
            const selectedComponents = (await window.postMessage(GET_SELECTED_COMPONENTS, filterType))[0] || [];
            callback({ selectedComponents });
        };
        window.addEventListener('sketchSelectionChange', selectionListeners[listenerId], true);
        selectionListeners[listenerId](); // init with current selection
    }
    removeSelectionChangeListener(listenerId) {
        window.removeEventListener('sketchSelectionChange', selectionListeners[listenerId]);
        delete selectionListeners[listenerId];
    }
    async exportToWebApp(selectedFrames) {
        //@ts-ignore
        return window.postMessage(EXPORT_TO_WEB_APP, selectedFrames).then(arr => arr[0]);
    }
    async getTextComponentsValue(componentsIds) {
        //@ts-ignore
        return window.postMessage(GET_TEXT_COMPONENTS_VALUE, componentsIds).then(arr => arr[0]);
    }
    async setTextComponentsValue(components) {
        //@ts-ignore
        return window.postMessage(SET_TEXT_COMPONENTS_VALUE, components).then(arr => arr[0]);
    }
    async runInEditContext(asyncFunc) {
        return asyncFunc();
    }
    selectComponent(componentId, isZoomToMax = false) {
        window.postMessage(SELECT_COMPONENT, componentId);
    }
    async setLocalStorage(key, value) {
        const paramString = JSON.stringify({ key, value });
        return window.postMessage(SET_LOCAL_STORAGE, paramString);
    }
    async getLocalStorage(key) {
        //@ts-ignore
        return window.postMessage(GET_LOCAL_STORAGE, key).then(arr => arr[0]);
    }
    async setSessionInLocalStorage(sessionId) {
        this.setLocalStorage(SESSION_ID_NAME, sessionId);
    }
    async getSessionFromLocalStorage() {
        return this.getLocalStorage(SESSION_ID_NAME);
    }
    async clearSessionFromLocalStorage() {
        this.setLocalStorage(SESSION_ID_NAME, undefined);
    }
}
export const sketchPlatformAPI = new SketchPlatformAPI();
