import * as React from 'react';
import classNames from "classnames";
import CircleSpinner from '../../../../commons/assets/spinner.svg';
import './Spinner.scss';
export default class Spinner extends React.Component {
    static get defaultProps() {
        return {
            size: "small",
            theme: "light"
        };
    }
    render() {
        const { theme, size, className } = this.props;
        return React.createElement("div", { className: classNames("spinner", className, theme, size) },
            React.createElement(CircleSpinner, null));
    }
}
