import { action } from "typesafe-actions";
import { Constants } from "./constants";
import * as discussionApi from "../../../../commons/api/discussion";
import * as logger from "../../../../commons/utils/logger";
import { promptBell } from "../app/actions";
export function submitDiscussionComment({ textId, textRevisionId, discussionId, message }) {
    return async (dispatch, getState) => {
        const store = getState();
        const { text, app } = store;
        const { user } = app;
        const { id: authorId } = user;
        const { managedTexts, currentProjectId: projectId } = text;
        const discussedText = Object.values(managedTexts).find(({ id }) => id === textId) || {};
        const { frameId } = discussedText;
        try {
            const { commentId, discussionId: commentDiscussionId } = await discussionApi.submitDiscussionComment({ projectId, frameId, textId, textRevisionId, discussionId, message });
            const isNewDiscussionCreated = !discussionId && commentDiscussionId;
            if (isNewDiscussionCreated) {
                dispatch(action(Constants.CREATE_DISCUSSION, { discussionId: commentDiscussionId, projectId, frameId, textId, textRevisionId }));
            }
            dispatch(action(Constants.SUBMIT_COMMENT, { commentId, discussionId: commentDiscussionId, authorId, message }));
            return { commentId, discussionId: commentDiscussionId };
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to add a comment. Please try again in a few minutes or contact us at support@frontitude.com"
            }));
        }
    };
}
export function toggleResolvedState({ textId, discussionId }) {
    return async (dispatch, getState) => {
        const store = getState();
        const { text, app } = store;
        const { user } = app;
        const { id: userId } = user;
        const { currentProjectId: projectId } = text;
        try {
            await discussionApi.toggleResolvedState({ projectId, discussionId });
            dispatch(action(Constants.TOGGLE_RESOLVED_STATE, { discussionId, userId }));
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to toggle state. Please try again in a few minutes or contact us at support@frontitude.com"
            }));
        }
    };
}
