const HEBREW_ARABIC_CHAR_REGEX = /[\u0590-\u06FF]/;
export const sortByDateProperty = (array, key, isDesc = true) => {
    return array.sort((a, b) => {
        return isDesc ?
            //@ts-ignore
            new Date(b[key]) - new Date(a[key]) :
            //@ts-ignore
            new Date(a[key]) - new Date(b[key]);
    });
};
export const isRtl = (s) => {
    return HEBREW_ARABIC_CHAR_REGEX.test(s);
};
/**
 * @param conditionFn Promise<boolean> asynchronous function that returns true or false
 * @param timeoutMS
 */
export const waitTil = async (conditionFn, timeoutMS = 1000 * 60) => {
    return new Promise((resolve, reject) => {
        const conditionIntervalId = setInterval(async () => {
            try {
                if (await conditionFn()) {
                    clearInterval(conditionIntervalId);
                    resolve();
                }
            }
            catch (error) {
                clearInterval(conditionIntervalId);
                reject(new Error(`error while running conditionFn. [error] ${error.toString()}`));
            }
        }, 500);
        const callTimeout = setTimeout(() => {
            clearInterval(conditionIntervalId);
            reject(new Error(`TIMEOUT_REACHED`));
        }, timeoutMS);
    });
};
