import * as React from "react";
import "./CommentForm.scss";
import MentionInput from "../MentionInput/MentionInput";
import Button from "../../components/Button/Button";
import * as logger from "../../../../commons/utils/logger";
import { bindActionCreators } from "redux";
import * as AppActions from "../../store/app/actions";
import { connect } from "react-redux";
import * as DiscussionsActions from "../../store/discussions/actions";
import Avatar from "../../components/Avatar/Avatar";
import classNames from "classnames";
class CommentForm extends React.Component {
    constructor(props) {
        super(props);
        this._isMounted = false;
        this.formEl = null;
        this.state = {
            isCommentSubmitInProgress: false,
            isCommentSubmitError: false,
            commentText: ""
        };
        this.handleCommentSubmit = this.handleCommentSubmit.bind(this);
        this.handleFormKeyDown = this.handleFormKeyDown.bind(this);
    }
    handleCommentSubmit(e) {
        const { appActions, discussionsActions, textId, textRevisionId, discussionId, onNewDiscussionCreated, onCommentSubmit } = this.props;
        const { commentText } = this.state;
        const { promptBell } = appActions;
        const { submitDiscussionComment } = discussionsActions;
        const note = commentText && commentText.replace(/\s+/g, ' ').trim();
        e.preventDefault();
        if (!note) {
            return;
        }
        this.setState({
            isCommentSubmitInProgress: true
        }, async () => {
            try {
                const { discussionId: commentDiscussionId } = await submitDiscussionComment({ textId, textRevisionId, discussionId, message: note });
                if (!discussionId && commentDiscussionId && onNewDiscussionCreated) {
                    await onNewDiscussionCreated(commentDiscussionId);
                }
                onCommentSubmit && await onCommentSubmit();
                if (this._isMounted) {
                    this.setState({
                        isCommentSubmitInProgress: false,
                        isCommentSubmitError: false,
                        commentText: ""
                    });
                }
            }
            catch (error) {
                logger.error(error, { textId });
                promptBell({
                    type: "error",
                    message: "Could not submit comment. Try again or contact support@frontitude.com"
                });
                this.setState({
                    isCommentSubmitInProgress: false,
                    isCommentSubmitError: true
                });
            }
        });
    }
    handleFormKeyDown(e) {
        const { isCommentSubmitInProgress } = this.state;
        if (e.keyCode == 13 && e.metaKey && !isCommentSubmitInProgress) {
            this.handleCommentSubmit(e);
        }
    }
    componentDidMount() {
        this._isMounted = true;
        this.formEl && this.formEl.addEventListener('keydown', this.handleFormKeyDown);
    }
    componentWillUnmount() {
        this._isMounted = false;
        this.formEl && this.formEl.removeEventListener('keydown', this.handleFormKeyDown);
    }
    render() {
        const { collaborators, user, autoFocus, className } = this.props;
        const { isCommentSubmitInProgress, commentText } = this.state;
        return React.createElement("form", { ref: (el) => this.formEl = el, className: classNames("comment-form", className), onSubmit: this.handleCommentSubmit },
            React.createElement("div", { className: "input-container" },
                React.createElement(Avatar, { user: user }),
                React.createElement(MentionInput, { className: "comment-input", value: commentText, autoFocus: autoFocus, placeholder: collaborators.length === 1 ? "Add a comment..." : undefined, onChange: (e, newValue, newPlainTextValue, mentions) => {
                        this.setState({
                            commentText: e.target.value
                        });
                    } })),
            React.createElement(Button, { className: "send-button", theme: "secondary", isLoading: isCommentSubmitInProgress }, "Send"));
    }
}
const mapStateToProps = ({ app }) => {
    const { user, account, config } = app;
    const { collaborators } = account;
    return {
        user,
        account,
        config,
        collaborators
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        appActions: bindActionCreators(AppActions, dispatch),
        discussionsActions: bindActionCreators(DiscussionsActions, dispatch)
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(CommentForm);
