import * as React from 'react';
import { SELECT_RANDOM_TEXT_COMPONENT } from "../constants/PluginMessageTypes";
import { platformAPI } from '../platformAPI';
export default function withSelection(WrappedComponent, { filterType, listenerId }) {
    return class extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                selectedComponents: []
            };
            this.handleSelectRandomTextComponent = this.handleSelectRandomTextComponent.bind(this);
        }
        async handleSelectRandomTextComponent() {
            parent.postMessage({ pluginMessage: { type: SELECT_RANDOM_TEXT_COMPONENT } }, '*');
        }
        componentDidMount() {
            platformAPI.addSelectionChangeListener(listenerId, filterType, ({ selectedComponents } = { selectedComponents: [] }) => {
                this.setState({ selectedComponents });
            });
        }
        componentWillUnmount() {
            platformAPI.removeSelectionChangeListener(listenerId);
        }
        render() {
            const { selectedComponents } = this.state;
            return React.createElement(WrappedComponent, Object.assign({}, this.props, { selectRandomTextComponent: this.handleSelectRandomTextComponent, selectedComponents: selectedComponents }));
        }
    };
}
