import * as Sentry from "@sentry/browser";
let initializedPlatformDetails;
export const init = (platformDetails = null) => {
    initializedPlatformDetails = platformDetails;
    window.__PLUGIN_VERSION = GIT_COMMIT_ID;
    if (IS_PRODUCTION_ENV !== "true") {
        if (platformDetails) {
            platformDetails.displayName && console.log(`Platform: ${platformDetails.displayName}`);
            platformDetails.version && console.log(`Platform Version: ${platformDetails.version}`);
            platformDetails.os && console.log(`OS: ${platformDetails.os}`);
        }
        return;
    }
    Sentry.init({
        dsn: "https://da269d298d4a46308856e4d4e6f678e6@sentry.io/2492416",
        beforeBreadcrumb(breadcrumb, hint) {
            if (breadcrumb.category === 'ui.click') {
                const innerText = (hint.event.toElement && hint.event.toElement.innerText) || '';
                if (innerText) {
                    breadcrumb.message += ` "${innerText}"`;
                }
            }
            return breadcrumb;
        }
    });
    platformDetails && Sentry.configureScope(function (scope) {
        platformDetails.displayName && scope.setTag("Platform", platformDetails.displayName);
        platformDetails.version && scope.setTag("PlatformVersion", platformDetails.version);
        platformDetails.os && scope.setTag("os", platformDetails.os);
    });
};
export const error = (error, extra = {}) => {
    initializedPlatformDetails && initializedPlatformDetails.displayName
        && (error.name = `[${initializedPlatformDetails.displayName}] ${error.name}`);
    if (IS_PRODUCTION_ENV !== "true") {
        console.error(error, extra);
        return;
    }
    Sentry.withScope(scope => {
        scope.setExtras(extra);
        scope.setLevel(Sentry.Severity.Error);
        Sentry.captureException(error);
    });
};
export const info = (message, data = {}) => {
    if (IS_PRODUCTION_ENV !== "true") {
        console.log(message, data);
        return;
    }
    Sentry.addBreadcrumb({
        data,
        message,
        level: Sentry.Severity.Info
    });
};
