import { getRectanglesOverlappingAreaSize } from './math';
export const getNodePositionWithinParent = (node, parentNode) => {
    const MIN_NODE_OVERLAP_PERCENTAGE = 30;
    const overlapAreaSize = getNodesOverlappingAreaSize(node, parentNode);
    const nodeOverlapPercentage = overlapAreaSize / (node.width * node.height) * 100;
    if (nodeOverlapPercentage > MIN_NODE_OVERLAP_PERCENTAGE) {
        return {
            x: node.x - parentNode.x,
            y: node.y - parentNode.y
        };
    }
    else {
        return null;
    }
};
export const getNodesOverlappingAreaSize = (node1, node2) => {
    return getRectanglesOverlappingAreaSize({ leftX: node1.x, rightX: node1.x + node1.width, bottomY: node1.y, topY: node1.y + node1.height }, { leftX: node2.x, rightX: node2.x + node2.width, bottomY: node2.y, topY: node2.y + node2.height });
};
