import { action } from 'typesafe-actions';
import { push } from 'connected-react-router';
import { Constants } from './constants';
import * as authApi from "../../../../commons/api/auth";
import { EXPORT_TAB, INTRO_SCREEN } from "../../constants/PluginPathTypes";
import { GOOGLE_SIGN_IN, MICROSOFT_AZURE_SIGN_IN } from "../../constants/ExternalRouteTypes";
import { HANDLE_REACTIVE_ACTION, LISTEN_REACTIVE_ACTIONS } from "../../constants/PluginMessageTypes";
import * as miscApi from "../../../../commons/api/misc";
import * as messenger from '../../utils/messenger';
import { identify } from "../../../../commons/utils/analytics";
import * as logger from "../../../../commons/utils/logger";
import { getClientPublisherId } from "../../utils/misc";
import { platformAPI } from '../../platformAPI';
import { GOOGLE_AUTH } from '../../constants/AuthenticationProviderTypes';
import * as userApi from '../../../../commons/api/user';
const PULL_CHANGES_CONFIG_KEY = "pullChangesConfig";
export function initState() {
    return (dispatch, getState) => initStateInternal(dispatch, getState);
}
export function setUserDetails(user) {
    return action(Constants.SET_USER_DETAILS, user);
}
export function promptBell({ type, message, duration, onClose }) {
    return (dispatch) => {
        dispatch(action(Constants.SHOW_BELL, { type, message, onClose }));
        if (type !== "info") {
            setTimeout(() => {
                dispatch(hideBell());
            }, duration || (type === "error" ? 8 * 1000 : 2 * 1000));
        }
    };
}
export function hideBell() {
    return action(Constants.HIDE_BELL);
}
export function addRequestedFeature(featureName) {
    return action(Constants.REQUEST_FEATURE, featureName);
}
export function setOnboardingMode(isOnboardingMode) {
    return action(Constants.SET_ONBOARDING_MODE, isOnboardingMode);
}
export function login(tempSessionId, authProviderType) {
    return async (dispatch, getState) => {
        const route = (authProviderType === GOOGLE_AUTH) ? GOOGLE_SIGN_IN : MICROSOFT_AZURE_SIGN_IN;
        platformAPI.openExternalURL(`${route}?figsessid=${tempSessionId}&originPlatform=${platformAPI.getPlatformTerminology().displayName}`);
        const { isPersistSessionUsingLocalStorage } = platformAPI.getPlatformSettings();
        const { sessionId } = await authApi.persistSessionCookiePolling(tempSessionId, isPersistSessionUsingLocalStorage);
        if (sessionId) {
            await platformAPI.setSessionInLocalStorage(sessionId);
        }
        await initStateInternal(dispatch, getState);
    };
}
export function logout() {
    return async (dispatch) => {
        try {
            if (platformAPI.getPlatformSettings().isPersistSessionUsingLocalStorage) {
                await platformAPI.clearSessionFromLocalStorage();
            }
            await authApi.logout();
            dispatch(setUserDetails(null));
            dispatch(push(INTRO_SCREEN));
        }
        catch (error) {
            logger.error(error);
            dispatch(promptBell({
                type: "error",
                message: "Failed to logout, please try again in a while"
            }));
        }
    };
}
function setState(state) {
    return action(Constants.SET_STATE, state);
}
async function initStateInternal(dispatch, getState) {
    const fileInfo = await platformAPI.getFileInfo();
    const platformIdentifiers = await platformAPI.getPlatformIdentifiers();
    let pullConfig = {};
    try {
        pullConfig = await platformAPI.getLocalStorage(PULL_CHANGES_CONFIG_KEY);
    }
    catch (error) {
        logger.error(error, { fileInfo, message: `Get ${PULL_CHANGES_CONFIG_KEY} from local storage at init throws an error` });
    }
    if (!fileInfo || !fileInfo.id) {
        logger.error(new Error("Bad file info object found"), { fileInfo });
        return;
    }
    const stateFromServer = await miscApi.initState({
        figmaFileId: fileInfo.id,
        figmaFileName: fileInfo.name,
        platformId: platformIdentifiers.id
    });
    dispatch(setState(stateFromServer));
    dispatch(updatePullConfig(pullConfig));
    const store = getState();
    const { app } = store;
    const { user, lastStateFromServerSetAt, account } = app;
    const { collaborators } = account;
    if (user) {
        logger.init();
        identify(user.id);
        if (collaborators.length > 1 && platformAPI.getPlatformSettings().isSupportsReactiveActions) {
            await listenReactiveActions(dispatch, lastStateFromServerSetAt);
        }
        dispatch(push(EXPORT_TAB));
    }
    else {
        dispatch(push(INTRO_SCREEN));
    }
}
const listenReactiveActions = async (dispatch, lastStateFromServerSetAt) => {
    try {
        const publisherId = await getClientPublisherId();
        dispatch(setReactiveActionsPublisherId(publisherId));
        logger.info("Client publisher ID is set", { publisherId });
        parent.postMessage({ pluginMessage: { type: LISTEN_REACTIVE_ACTIONS, publisherId } }, "*");
    }
    catch (error) {
        logger.error(error);
    }
    messenger.setMessageHandler(HANDLE_REACTIVE_ACTION, (message = {}) => {
        const { pendingActions } = message;
        logger.info("Reactive actions queue", { pendingActions });
        logger.info("Replaying reactive actions", { pendingActions: pendingActions
                .filter(({ publishedAt }) => publishedAt > lastStateFromServerSetAt) });
        pendingActions
            .filter(({ publishedAt }) => publishedAt > lastStateFromServerSetAt)
            .forEach((action) => { dispatch(replayReactiveAction(action)); });
    });
    logger.info("Started listening to reactive actions...");
};
export function requestFeature(featureName) {
    return async (dispatch) => {
        try {
            await miscApi.requestFeature({ featureName });
            dispatch(addRequestedFeature(featureName));
        }
        catch (error) {
            logger.error(error, { featureName });
            dispatch(promptBell({
                type: "error",
                message: "Failed to request feature, please try again in a while"
            }));
        }
    };
}
export function replayReactiveAction({ type, payload }) {
    const replayedActionData = { isReplayed: true };
    if (payload) {
        return action(type, Object.assign({}, payload, replayedActionData));
    }
    return action(type, replayedActionData);
}
export function setReactiveActionsPublisherId(publisherId) {
    return action(Constants.SET_REACTIVE_ACTIONS_PUBLISHER_ID, { publisherId });
}
export function completeProductTour(tourId) {
    return async (dispatch) => {
        try {
            await userApi.addCompletedTour({ tourId });
            dispatch(action(Constants.COMPLETE_TOUR, { tourId }));
        }
        catch (error) {
            logger.error(error);
        }
    };
}
export function updatePullConfig(configChange) {
    return async (dispatch, getState) => {
        const store = getState();
        const { app } = store;
        const { config } = app;
        const { pullChanges } = config;
        const newConfig = Object.assign({}, pullChanges, configChange);
        try {
            try { // persistent config - it's okay if there's an error, we can continue without it
                await platformAPI.setLocalStorage(PULL_CHANGES_CONFIG_KEY, newConfig);
            }
            catch (localStorageError) {
                logger.error(localStorageError, { message: `Set ${PULL_CHANGES_CONFIG_KEY} in local storage throws an error. proceeding without.` });
            }
            dispatch(action(Constants.UPDATE_CONFIG, {
                configChange: {
                    pullChanges: newConfig
                }
            }));
        }
        catch (error) {
            logger.error(error);
        }
    };
}
