import fetch from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function submitDiscussionComment({ projectId, frameId, textId, textRevisionId, discussionId, message }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ projectId, frameId, textId, textRevisionId, discussionId, message })
    };
    return fetch(`${FRONTEND_HOSTNAME}/discussions/submit_comment`, params);
}
export function toggleResolvedState({ projectId, discussionId }) {
    const params = {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        credentials: "include",
        type: "json",
        body: JSON.stringify({ projectId, discussionId })
    };
    return fetch(`${FRONTEND_HOSTNAME}/discussions/toggle_resolved_state`, params);
}
