import fetchNative from './fetch';
import fetch from './fetch-gw';
import { fetchPolling } from './fetch-gw';
import { FRONTEND_HOSTNAME } from "../../Plugin/core/constants/EndpointTypes";
export function pullChangesFromGoogleSheets(projectId) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ projectId })
    };
    return fetch(`${FRONTEND_HOSTNAME}/integrations/google-sheets/pull`, params);
}
export function pullChangesFromFrontitude({ projectId, textStatus, selection }) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ projectId, selection, filters: { textStatus } })
    };
    return fetch(`${FRONTEND_HOSTNAME}/text/plugin_pull`, params);
}
export function getConnectedSheetPolling(projectId) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ projectId })
    };
    return fetchPolling(`${FRONTEND_HOSTNAME}/integrations/google-sheets`, params);
}
export function connectPreviouslyConnectedSheet({ projectId, figmaFileName, sheetId }) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ projectId, figmaFileName, sheetId })
    };
    return fetchNative(`/integrations/google-sheets/connect-previously-connected`, params);
}
export function disconnectGoogleSheet({ projectId }) {
    const params = {
        method: 'POST',
        credentials: 'include',
        type: 'json',
        body: JSON.stringify({ projectId })
    };
    return fetch(`${FRONTEND_HOSTNAME}/integrations/google-sheets/disconnect`, params);
}
