import * as messenger from "../../Plugin/core/utils/messenger";
import fetchNative from "./fetch";
import { platformAPI } from "../../Plugin/core/platformAPI";
const FT_SES_HEADER_NAME = 'X-FT-Ses';
export default async function fetch(url, params) {
    let sessionId;
    if (platformAPI.getPlatformSettings().isPersistSessionUsingLocalStorage) {
        sessionId = await platformAPI.getSessionFromLocalStorage();
    }
    const headers = Object.assign({}, sessionId ? {
        [FT_SES_HEADER_NAME]: sessionId
    } : {}, params.type === "json" ? {
        "Accept": "application/json",
        "Content-Type": "application/json"
    } : {});
    const paramsWrap = Object.assign({}, params, {
        credentials: "same-origin",
        headers
    });
    const payload = {
        type: "fetch",
        url,
        params: paramsWrap
    };
    const apiGwIframe = document.getElementById("api-gw");
    try {
        if (!apiGwIframe) {
            const response = await fetchNative(url, params);
            return response;
        }
        else {
            const response = await messenger.callGateway(apiGwIframe.contentWindow, payload, "*");
            const { body, error } = response;
            if (error) {
                throw error;
            }
            return body;
        }
    }
    catch (error) { // normalize the error and re-throw
        if (!(error instanceof Error)) {
            const name = `HTTP_CODE_${error.code}`;
            error = new Error(`fetch error. [url] ${url} [code] ${error.code}`);
            error.name = name;
        }
        throw error;
    }
}
export function fetchPolling(url, params) {
    return new Promise((resolve, reject) => {
        const pollInterval = setInterval(async () => {
            try {
                const response = await fetch(url, params);
                if (!response) {
                    return;
                }
                clearInterval(pollInterval);
                clearTimeout(pollTimeout);
                resolve(response);
            }
            catch (error) {
                if (error && error.name === 'HTTP_CODE_404') {
                    return;
                }
                clearInterval(pollInterval);
                clearTimeout(pollTimeout);
                reject(error);
            }
        }, 1000);
        const pollTimeout = setTimeout(() => {
            clearInterval(pollInterval);
        }, 15 * 60 * 1000);
    });
}
