export const Utf8ArrayToStr = (array) => {
    var out, i, len, c;
    var char2, char3;
    out = "";
    len = array.length;
    i = 0;
    while (i < len) {
        c = array[i++];
        switch (c >> 4) {
            case 0:
            case 1:
            case 2:
            case 3:
            case 4:
            case 5:
            case 6:
            case 7:
                // 0xxxxxxx
                out += String.fromCharCode(c);
                break;
            case 12:
            case 13:
                // 110x xxxx   10xx xxxx
                char2 = array[i++];
                out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
                break;
            case 14:
                // 1110 xxxx  10xx xxxx  10xx xxxx
                char2 = array[i++];
                char3 = array[i++];
                out += String.fromCharCode(((c & 0x0F) << 12) |
                    ((char2 & 0x3F) << 6) |
                    ((char3 & 0x3F) << 0));
                break;
        }
    }
    return out;
};
// Taken from https://gist.github.com/joni/3760795/8f0c1a608b7f0c8b3978db68105c5b1d741d0446
export const stringToUTF8Array = (str) => {
    var utf8 = [];
    for (var i = 0; i < str.length; i++) {
        var charcode = str.charCodeAt(i);
        if (charcode < 0x80)
            utf8.push(charcode);
        else if (charcode < 0x800) {
            utf8.push(0xc0 | (charcode >> 6), 0x80 | (charcode & 0x3f));
        }
        else if (charcode < 0xd800 || charcode >= 0xe000) {
            utf8.push(0xe0 | (charcode >> 12), 0x80 | ((charcode >> 6) & 0x3f), 0x80 | (charcode & 0x3f));
        }
        // surrogate pair
        else {
            i++;
            charcode = ((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff);
            utf8.push(0xf0 | (charcode >> 18), 0x80 | ((charcode >> 12) & 0x3f), 0x80 | ((charcode >> 6) & 0x3f), 0x80 | (charcode & 0x3f));
        }
    }
    return utf8;
};
export const convertSvgToJpegImage = async (svgUint8Array, width, height, { returnedValue = 'DataURL' } = {}) => {
    const IMAGE_MIME_TYPE = "image/jpeg";
    const IMAGE_QUALITY = 0.8;
    return new Promise((resolve, reject) => {
        const svgBlob = new Blob([svgUint8Array], { type: "image/svg+xml" });
        var DOMURL = window.URL || window.webkitURL || window;
        //@ts-ignore
        var url = DOMURL.createObjectURL(svgBlob);
        var canvas = document.createElement("canvas");
        canvas.width = width;
        canvas.height = height;
        var canvasContext = canvas.getContext("2d");
        var img = new Image();
        img.width = width;
        img.height = height;
        img.onload = function () {
            try {
                //@ts-ignore
                DOMURL.revokeObjectURL(url); // no longer need to read the blob so it's revoked
                canvasContext.drawImage(img, 0, 0);
                if (returnedValue === 'DataURL') {
                    const png = canvas.toDataURL(IMAGE_MIME_TYPE, IMAGE_QUALITY);
                    resolve(png);
                }
                else {
                    canvas.toBlob(blob => resolve(blob), IMAGE_MIME_TYPE, IMAGE_QUALITY);
                }
            }
            catch (err) {
                reject(err);
            }
        };
        img.onerror = () => {
            reject(`There was an error loading the data URI as an image`);
        };
        img.src = url;
        // on XD the image must be appended to the document. decided to implement it in general just in case other browsers act the same
        // const imgWrapper = document.createElement('div')
        // imgWrapper.style.position = 'fixed'
        // imgWrapper.style.top = '0'
        // imgWrapper.style.left = '0'
        // imgWrapper.style.overflow = 'hidden'
        // imgWrapper.style.width = '1px'
        // imgWrapper.style.height = '1px'
        // imgWrapper.style.opacity = '0'
        // imgWrapper.appendChild(img)
        // document.body.appendChild(imgWrapper)
    });
};
