import { generateRandomString } from "./misc";
import { waitTil } from "../../../commons/utils/misc";
const messageTypeToHandler = {};
export const setMessageHandler = (type, handler) => {
    messageTypeToHandler[type] = handler;
};
export const removeMessageHandler = (type) => {
    delete messageTypeToHandler[type];
};
export const startListen = () => {
    window.onmessage = event => {
        const message = event.data.pluginMessage || event.data;
        const { type } = message;
        messageTypeToHandler[type] && messageTypeToHandler[type](message);
    };
};
export const stopListen = () => {
    window.onmessage = null;
};
export const callFigma = (event, payload = {}, timeoutInMS = 1000 * 30) => {
    return new Promise((resolve, reject) => {
        const messengerEvent = generateRandomString(8);
        const payloadWithEvent = {
            pluginMessage: {
                type: event,
                payload,
                messengerEvent
            }
        };
        const callTimeout = setTimeout(() => {
            removeMessageHandler(messengerEvent);
            reject(new Error(`${event}: TIMEOUT_REACHED`));
        }, timeoutInMS);
        setMessageHandler(messengerEvent, ({ response, error }) => {
            clearTimeout(callTimeout);
            if (error) {
                reject(error);
                return;
            }
            resolve(response);
        });
        parent.postMessage(payloadWithEvent, "*");
    });
};
export const callGateway = async (receiverWindow, payload, origin = "*") => {
    if (!window.isFrontitudeApiGatewayLoaded) {
        await waitTil(async () => window.isFrontitudeApiGatewayLoaded);
    }
    return new Promise((resolve, reject) => {
        const tempEvent = generateRandomString(8);
        const payloadWithEvent = Object.assign({}, payload, {
            event: tempEvent
        });
        const callTimeout = setTimeout(() => {
            removeMessageHandler(tempEvent);
            reject(new Error(`${payloadWithEvent.url}: TIMEOUT_REACHED`));
        }, 60 * 1000);
        setMessageHandler(tempEvent, (response) => {
            clearTimeout(callTimeout);
            resolve(response);
        });
        receiverWindow.postMessage(payloadWithEvent, origin);
    });
};
