import * as React from 'react';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import withSelection from "../../components/withSelection";
import CopyTabSelectedSingle from "../../components/CopyTabSelectedSingle/CopyTabSelectedSingle";
import CopyTabSelectedNone from "../../components/CopyTabSelectedNone/CopyTabSelectedNone";
import CopyTabSelectedUnmanaged from "../../components/CopyTabSelectedUnmanaged/CopyTabSelectedUnmanaged";
import * as TextActions from "../../store/text/actions";
import * as logger from "../../../../commons/utils/logger";
import { getLinkedTexts } from "../../utils/text";
class CopyTab extends React.Component {
    constructor(props) {
        super(props);
        this.copyTabSelectedSingle = null;
        this.state = {
            isChangeSubmitInProgress: false,
            isChangeSubmitError: false
        };
        this.getSelectedTextComponents = this.getSelectedTextComponents.bind(this);
        this.getLinkedTexts = this.getLinkedTexts.bind(this);
        this.handleChangeSubmit = this.handleChangeSubmit.bind(this);
        this.handleExportParentFrame = this.handleExportParentFrame.bind(this);
        this.handleAttachTag = this.handleAttachTag.bind(this);
        this.handleDetachTag = this.handleDetachTag.bind(this);
        this.handleCreateNewTag = this.handleCreateNewTag.bind(this);
        this.handleNewDiscussionCreated = this.handleNewDiscussionCreated.bind(this);
    }
    static get defaultProps() {
        return {
            selectedComponents: []
        };
    }
    getSelectedTextComponents() {
        const { selectedComponents } = this.props;
        return selectedComponents.filter(({ type }) => type === 'TEXT');
    }
    getLinkedTexts() {
        const { managedTexts } = this.props;
        const selectedTextComponents = this.getSelectedTextComponents();
        const selectedManagedText = selectedTextComponents.length === 1 ? managedTexts[selectedTextComponents[0].nodeId] : null;
        if (selectedManagedText) {
            const linkedTexts = getLinkedTexts(managedTexts, selectedManagedText.id)
                .map(({ id, nodeId, lastChange }) => ({
                textId: id,
                nodeId,
                designProperties: lastChange.designProperties
            }));
            return linkedTexts;
        }
        return [];
    }
    async handleExportParentFrame() {
        const { textActions } = this.props;
        const { exportComponents } = textActions;
        const selectedTextComponents = this.getSelectedTextComponents();
        const selectedFrames = []; //TODO Get parent frame
        await exportComponents(selectedFrames);
    }
    handleChangeSubmit({ note, isCommentOnly }) {
        const { textActions } = this.props;
        const { submitTextChange } = textActions;
        const selectedTextComponents = this.getSelectedTextComponents();
        const linkedTextIds = this.getLinkedTexts().map(({ nodeId }) => nodeId);
        const { figmaTextId, text } = selectedTextComponents[0];
        this.setState({
            isChangeSubmitInProgress: true,
            isChangeSubmitError: false
        }, async () => {
            try {
                await submitTextChange({
                    figmaTextId,
                    note,
                    text: isCommentOnly ? null : text,
                    linkedTextIds
                });
                this.setState({
                    isChangeSubmitInProgress: false
                });
            }
            catch (error) {
                logger.error(error, { figmaTextId });
                this.setState({
                    isChangeSubmitInProgress: false,
                    isChangeSubmitError: true
                });
            }
        });
    }
    async handleAttachTag(tag) {
        const { textActions, managedTexts } = this.props;
        const { attachTagToText } = textActions;
        const selectedTextComponents = this.getSelectedTextComponents();
        const managedText = managedTexts[selectedTextComponents[0].figmaTextId];
        await attachTagToText({ managedText, tag });
    }
    async handleDetachTag(tag) {
        const { textActions, managedTexts } = this.props;
        const { detachTagFromText } = textActions;
        const selectedTextComponents = this.getSelectedTextComponents();
        const managedText = managedTexts[selectedTextComponents[0].figmaTextId];
        await detachTagFromText({ managedText, tag });
    }
    async handleCreateNewTag(tagLabel) {
        const { textActions, managedTexts } = this.props;
        const { createNewTag, attachTagToText } = textActions;
        const selectedTextComponents = this.getSelectedTextComponents();
        const managedText = managedTexts[selectedTextComponents[0].figmaTextId];
        const newTag = await createNewTag({ managedText, tagLabel });
        await attachTagToText({ managedText, tag: newTag });
    }
    async handleNewDiscussionCreated(discussionId, entry) {
        const { textActions } = this.props;
        const { updateTextHistoryEntry } = textActions;
        const { textId, createdAt: entryId } = entry;
        const updates = { discussionId };
        await updateTextHistoryEntry({ textId, entryId, updates });
    }
    render() {
        const { selectRandomTextComponent, managedTexts, workspaceTags, collaborators, discussions } = this.props;
        const { isChangeSubmitError, isChangeSubmitInProgress } = this.state;
        const selectedTextComponents = this.getSelectedTextComponents();
        const managedTextsIds = Object.keys(managedTexts);
        const isAllSelectedTextManaged = selectedTextComponents.every(({ figmaTextId }) => managedTextsIds.indexOf(figmaTextId) > -1);
        const isMultipleManagedTextsSelected = selectedTextComponents.length > 1 && isAllSelectedTextManaged;
        const linkedTexts = this.getLinkedTexts();
        return React.createElement("div", { className: "plugin-tab" },
            (selectedTextComponents.length === 0 || selectedTextComponents.length > 1 || isMultipleManagedTextsSelected) &&
                React.createElement(CopyTabSelectedNone, { selectedTextComponents: selectedTextComponents, onRandomSelectClick: selectRandomTextComponent }),
            selectedTextComponents.length === 1 && !isAllSelectedTextManaged &&
                React.createElement(CopyTabSelectedUnmanaged, { onExportParentFrame: this.handleExportParentFrame }),
            selectedTextComponents.length === 1 && isAllSelectedTextManaged &&
                React.createElement(CopyTabSelectedSingle, { ref: el => this.copyTabSelectedSingle = el, selectedTextComponent: selectedTextComponents[0], managedText: managedTexts[selectedTextComponents[0].figmaTextId], linkedTexts: linkedTexts, workspaceTags: workspaceTags, onChangeSubmit: this.handleChangeSubmit, onAttachTag: this.handleAttachTag, onDetachTag: this.handleDetachTag, onCreateNewTag: this.handleCreateNewTag, isLoading: isChangeSubmitInProgress, isSubmitError: isChangeSubmitError, collaborators: collaborators, discussions: discussions, onNewDiscussionCreated: this.handleNewDiscussionCreated }));
    }
}
const mapStateToProps = ({ app, text, discussions: discussionsStore }) => {
    const { managedTexts, workspaceTags } = text;
    const { account } = app;
    const { plan, collaborators } = account;
    const { managedTextsLimit } = plan;
    const { discussions } = discussionsStore;
    return {
        managedTexts,
        managedTextsLimit,
        workspaceTags,
        collaborators,
        discussions
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        textActions: bindActionCreators(TextActions, dispatch)
    };
};
export default withSelection(connect(mapStateToProps, mapDispatchToProps)(CopyTab), { listenerId: "CopyTab", filterType: "TEXT" });
