import { getNodePositionWithinParent } from './designTool';
export class TextChangeDetector {
    constructor(figmaComponentId) {
        this.figmaComponentId = null;
        this.callback = null;
        this.checkInterval = null;
        this.lastValueChecked = "";
        this.figmaComponentId = figmaComponentId;
        const node = figma.getNodeById(figmaComponentId);
        this.lastValueChecked = node && node.type === "TEXT" ? node.characters : "";
    }
    onTextChange(callback) {
        this.callback = callback;
    }
    startListen() {
        this.checkInterval = setInterval(() => {
            const { selection } = figma.currentPage;
            if (selection.length !== 1 ||
                selection[0].type !== "TEXT" ||
                selection[0].id !== this.figmaComponentId) {
                this.stopListen();
                return;
            }
            const selectedText = selection[0];
            if (selectedText.characters !== this.lastValueChecked) {
                const newValue = selectedText.characters;
                this.callback(newValue);
                this.lastValueChecked = newValue;
            }
        }, 500);
    }
    stopListen() {
        clearInterval(this.checkInterval);
    }
}
export const getNodePath = (node) => {
    let currentNode = node.parent;
    let nodePath = [];
    while (currentNode && currentNode.type !== "DOCUMENT") {
        nodePath.unshift({ id: currentNode.id, name: currentNode.name });
        currentNode = currentNode.parent;
    }
    return nodePath;
};
export const getTopParentInPage = (node) => {
    const topParentDetails = getNodePath(node)[1];
    if (!topParentDetails) {
        return node;
    }
    return figma.getNodeById(topParentDetails.id);
};
export const collectComponentsByType = (type, parent) => {
    type = type.toUpperCase();
    let timer;
    let walker = walkTree(parent);
    function processOnce() {
        let results = [];
        let count = 0;
        let done = true;
        let res;
        while (!(res = walker.next()).done) {
            let node = res.value;
            if (node.type === type && !node.removed) {
                results.push(node);
            }
            if (++count === 1000) {
                done = false;
                timer = setTimeout(processOnce, 20);
                break;
            }
        }
        return results;
    }
    function* walkTree(node) {
        yield node;
        let children = node.children;
        if (children) {
            for (let child of children) {
                yield* walkTree(child);
            }
        }
    }
    return processOnce();
};
export const getChildrenTextComponents = (selectedComponents) => {
    return selectedComponents.reduce((accum, curr) => {
        const textComponents = collectComponentsByType("TEXT", curr);
        return textComponents.length > 0 ?
            accum.concat(textComponents) :
            accum;
    }, []);
};
export const isNodeVisible = (node) => {
    let currentNode = node;
    while (currentNode) {
        if (currentNode.removed === true || currentNode.visible === false) {
            return false;
        }
        currentNode = currentNode.parent;
    }
    return true;
};
export const getNodePositionWithinFrame = (node, frameNode, frameBoundingRectParam) => {
    const [nodePosX, nodePosY, nodeWidth, nodeHeight] = [node.absoluteTransform[0][2], node.absoluteTransform[1][2], node.width, node.height];
    const frameShadowWidths = getFrameShadowWidths(frameNode);
    const frameStrokeWidth = getFrameStrokeWidth(frameNode);
    const frameBoundingRect = frameBoundingRectParam || getFrameBoundingRect(frameNode);
    return getNodePositionWithinParent({
        x: nodePosX + frameShadowWidths.left + frameStrokeWidth,
        y: nodePosY + frameShadowWidths.top + frameStrokeWidth,
        width: nodeWidth,
        height: nodeHeight
    }, frameBoundingRect);
};
export const getNodeGlobalPosition = (node) => {
    return { x: node.absoluteTransform[0][2], y: node.absoluteTransform[1][2] };
};
export const isNodeExportable = (node) => {
    return node && isExportableType(node.type) && node.parent.type === "PAGE";
};
export const isExportableType = (nodeType) => {
    return nodeType && (nodeType === "FRAME" || nodeType === "COMPONENT" || nodeType === "INSTANCE");
};
export const isTopLevelFrame = (node) => {
    return node.type === "FRAME" && node.parent.type === "PAGE";
};
export const hasMixedStyle = (node) => {
    const potentialMixedStyleProperties = [
        node.fontSize,
        node.fontName,
        node.textCase,
        node.textDecoration,
        node.letterSpacing,
        node.lineHeight,
        node.textStyleId,
        node.fills,
        node.strokeCap,
        node.strokeJoin,
        node.fillStyleId
    ];
    return potentialMixedStyleProperties.some(prop => prop === figma.mixed);
};
export function getPath(nodeA, { isFramePath = false } = {}) {
    let node = nodeA;
    const path = [];
    while (isFramePath ? node.parent.type !== 'PAGE' : node.type !== 'DOCUMENT') {
        path.unshift(node);
        node = node.parent;
    }
    return path;
}
export function getMaskingNodeRecursive(originalNode, node) {
    if (!node || node.isMask) {
        return node;
    }
    if (isTopLevelFrame(node)) {
        return null;
    }
    const childrenMask = node.children && node.children.find((child) => child.isMask);
    if (childrenMask) {
        const originalNodeAncestor = node.children.indexOf(originalNode) > -1 ?
            originalNode :
            node.children.find((child) => {
                const originalAsDescendant = child.findAll((child) => child && child.id === originalNode.id && isNodeVisible(child));
                return originalAsDescendant.length > 0;
            });
        const childrenAncestorIndex = originalNodeAncestor && node.children.indexOf(originalNodeAncestor);
        const childrenMaskIndex = node.children.indexOf(childrenMask);
        if (childrenMask && originalNodeAncestor && childrenAncestorIndex > childrenMaskIndex) {
            return childrenMask;
        }
    }
    const maskSibling = node.parent && node.parent.children.find((child) => child.isMask);
    if (maskSibling) {
        const nodeIndex = node.parent.children.indexOf(node);
        const maskIndex = node.parent.children.indexOf(maskSibling);
        if (nodeIndex > maskIndex) {
            return maskSibling;
        }
    }
    return getMaskingNodeRecursive(originalNode, node.parent);
}
export function getMaskingNode(node) {
    return getMaskingNodeRecursive(node, node);
}
export function isLeafNode(nodeToFilter) {
    return (!nodeToFilter.children || nodeToFilter.children.length === 0) && //leaf
        isNodeVisible(nodeToFilter);
}
// Origin: https://github.com/figma-plugin-helper-functions/figma-plugin-helpers/blob/master/src/helpers/getBoundingRect.ts
/**
 *  this function return a bounding rect for an nodes
 */
// x/y absolute coordinates
// height/width
// x2/y2 bottom right coordinates
export function getFrameBoundingRect(frame) {
    function applyMatrixToPoint(matrix, point) {
        return [
            point[0] * matrix[0][0] + point[1] * matrix[0][1] + matrix[0][2],
            point[0] * matrix[1][0] + point[1] * matrix[1][1] + matrix[1][2]
        ];
    }
    const frameShadowWidths = getFrameShadowWidths(frame);
    const frameStrokeWidth = getFrameStrokeWidth(frame);
    const nodes = frame.children.concat(frame);
    const boundingRect = {
        x: 0,
        y: 0,
        x2: 0,
        y2: 0,
        height: 0,
        width: 0
    };
    if (frame.clipsContent) {
        const [nodePosX, nodePosY, nodeWidth, nodeHeight] = [frame.absoluteTransform[0][2], frame.absoluteTransform[1][2], frame.width, frame.height];
        return {
            x: nodePosX + frameStrokeWidth,
            y: nodePosY + frameStrokeWidth,
            x2: nodePosX + nodeWidth,
            y2: nodePosY + nodeHeight,
            height: nodeHeight + frameShadowWidths.left + frameShadowWidths.right + frameStrokeWidth * 2,
            width: nodeWidth + frameShadowWidths.top + frameShadowWidths.bottom + frameStrokeWidth * 2
        };
    }
    if (nodes.length > 0) {
        const xy = nodes.reduce((rez, node) => {
            const halfHeight = node.height / 2;
            const halfWidth = node.width / 2;
            const [[c0, s0, x], [s1, c1, y]] = node.absoluteTransform;
            const matrix = [
                [c0, s0, x + halfWidth * c0 + halfHeight * s0],
                [s1, c1, y + halfWidth * s1 + halfHeight * c1]
            ];
            // the coordinates of the corners of the rectangle
            const XY = {
                x: [1, -1, 1, -1],
                y: [1, -1, -1, 1]
            };
            // fill in
            for (let i = 0; i <= 3; i++) {
                const a = applyMatrixToPoint(matrix, [
                    XY.x[i] * halfWidth,
                    XY.y[i] * halfHeight
                ]);
                XY.x[i] = a[0];
                XY.y[i] = a[1];
            }
            XY.x.sort((a, b) => a - b);
            XY.y.sort((a, b) => a - b);
            rez.x.push(XY.x[0]);
            rez.y.push(XY.y[0]);
            rez.x2.push(XY.x[3]);
            rez.y2.push(XY.y[3]);
            return rez;
        }, { x: [], y: [], x2: [], y2: [] });
        const rect = {
            x: Math.min(...xy.x),
            y: Math.min(...xy.y),
            x2: Math.max(...xy.x2),
            y2: Math.max(...xy.y2)
        };
        boundingRect.x = rect.x;
        boundingRect.y = rect.y;
        boundingRect.x2 = rect.x2;
        boundingRect.y2 = rect.y2;
        boundingRect.width = rect.x2 - rect.x + frameShadowWidths.left + frameShadowWidths.right + frameStrokeWidth * 2;
        boundingRect.height = rect.y2 - rect.y + frameShadowWidths.top + frameShadowWidths.bottom + frameStrokeWidth * 2;
    }
    return boundingRect;
}
export function getFrameShadowWidths(frame) {
    const frameShadow = frame.effects.find(({ type }) => type === "DROP_SHADOW");
    const frameShadowSize = frameShadow && frameShadow.visible ? frameShadow.radius + frameShadow.spread : 0;
    if (!frameShadow || !frameShadow.visible) {
        return {
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        };
    }
    return {
        top: (frameShadow.offset.y * -1) + frameShadowSize,
        right: (frameShadow.offset.x < 0 ? 0 : frameShadow.offset.x) + frameShadowSize,
        bottom: (frameShadow.offset.y < 0 ? 0 : frameShadow.offset.y) + frameShadowSize,
        left: (frameShadow.offset.x * -1) + frameShadowSize
    };
}
export function getFrameStrokeWidth(frame) {
    if (frame.strokeAlign === "INSIDE") {
        return 0;
    }
    if (frame.strokeAlign === "CENTER") {
        return frame.strokeWeight / 2;
    }
    if (frame.strokeAlign === "OUTSIDE") {
        return frame.strokeWeight;
    }
}
