/**
 * (0,0) is positioned at the top left of the cordinate system.
 * taken from (replacing bottomY and topY): https://math.stackexchange.com/questions/99565/simplest-way-to-calculate-the-intersect-area-of-two-rectangles
 */
export const getRectanglesOverlappingAreaSize = (rect1, rect2) => {
    const xOverlap = Math.max(0, Math.min(rect1.rightX, rect2.rightX) - Math.max(rect1.leftX, rect2.leftX));
    const yOverlap = Math.max(0, Math.min(rect1.topY, rect2.topY) - Math.max(rect1.bottomY, rect2.bottomY));
    return xOverlap * yOverlap;
};
